time-selector {
	min-height: 0;
}

.time-selector {
	display: flex;
	flex-direction: column;

	.half {
		position: relative;
		height: @time-selector-row-height;
		line-height: 1;
		background-color: @cal-border-light;

		&.available {
			cursor: pointer;
			background-color: #fff;
		}

		// Borders between rows
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			width: 100%;
			height: 1px;
			background: @cal-border-dark;
			transition: background 0.1s;
			z-index: 1;
		}

		&.available::before {
			background: @cal-border-dark;
		}

		&.available:hover, &.selected {
			&::before { // Borders between rows
				background: @cal-reserved-own-color;
			}
		}

		&.reserved {
			cursor: auto;
		}


		// Group of reserved/selected rows
		.reserved-outline, .selected-outline {
			position: relative;
			top: 0;
			height: 100%;
			margin: 0 @time-selector-time-col-width;
			pointer-events: none;

			.reserved-outline-inner, .selected-outline-inner {
				display: table;
				position: absolute;
				top: 10px;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 3;
				text-align: center;
				background: @cal-reserved-color;
				color: #fff;
				text-transform: uppercase;
				border-radius: 3px;

				span {
					display: table-cell;
					vertical-align: middle;
					font-size: 13px;
				}
			}

			.reserved-outline-inner {
				text-transform: none;
				font-weight: normal;
			}

			.selected-outline-inner {
				background: transparent;
				overflow: hidden;

				.selected-from, .selected-to { // From/To selection markers
					position: absolute;
					top: 0;
					left: 0;
					width: 30%;
					min-width: 32px;
					height: 17px;
					border-radius: 3px;
					background: @cal-reserved-own-color;
					color: #fff;
					font-size: 11px;
					line-height: 17px;
				}
				.selected-from {
					z-index: 1;
				}
				.selected-to {
					left: auto;
					right: 0;
					top: auto;
					bottom: 0;
				}

				.diagonal-hatch { // SVG element for diagonal hatch pattern
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: -1;
				}
				.diagonal-hatch-pattern { // SVG diagonal hatch background pattern
					fill: none;
					stroke: @cal-reserved-own-color;
					stroke-width: 1;
				}
			}
		}

		&.reserved-own {
			.reserved-outline .reserved-outline-inner {
				background: @cal-reserved-own-color;
			}
		}

		// Time marker at left
		.time-cell {
			position: absolute;
			top: 0;
			left: 0;
			width: @time-selector-time-col-width;
			height: 100%;
			background-color: @cal-border-light;
			padding-left: 10px;
			transition: color 0.1s;
			z-index: 2;

			&::before {
				content: '';
				position: absolute;
				background: @cal-border-light;
				width: 88%;
				height: 80%;
				top: 10%;
				left: 5%;
				border-radius: 3px;
				transition: background 0.1s;
			}

			&::after {
				content: '';
				position: absolute;
				width: 0;
				height: 0;
				top: 50%;
				left: 94%;
				margin-left: -1px;
				margin-top: -7px;
				border-style: solid;
				border-width: 7px 0 7px 5px;
				border-color: transparent transparent transparent @cal-border-light;
				transition: border-color 0.1s;
			}

			&.time-cell-right {
				left: auto;
				right: 0;

				&::before {
					left: 7%;
				}

				&::after {
					left: 0;
					margin-left: 0;
					transform: rotate(180deg);
				}
			}
		}

		&.available .time-cell {
			background-color: #fff;
		}

		// Marker for others reservations
		&.reserved .time-cell, &.reserved:not(.reserved-own):hover .time-cell {
			color: #fff;

			&::before {
				background: @cal-reserved-color;
			}
			&::after {
				border-color: transparent transparent transparent @cal-reserved-color;
			}
		}
		// Marker for own reservations
		&.available:hover .time-cell, &.selected .time-cell, &.reserved-own .time-cell {
			color: #fff;

			&::before {
				background: @cal-reserved-own-color;
			}
			&::after {
				border-color: transparent transparent transparent @cal-reserved-own-color;
			}
		}

		.time {
			position: relative;
			display: inline-block;
			line-height: @time-selector-row-height;
		}

		&:not(.available) .time {
			opacity: 0.5;
		}

		sup {
			display: block;
			position: absolute;
			top: auto;
			bottom: 50%;
			left: 50%;
			padding-left: 18px;
			font-size: 10px;
			line-height: 1;
			opacity: 0.75;
		}
	}

	// Hint: Click an open time slot to select a start time
	.hint {
		flex: 0 0 auto;
		padding-bottom: 5px;
		font-size: 12px;
		text-transform: none;
		text-align: center;
		color: @gray-light;
	}

	.clear-selection {
		flex: 0 0 auto;
		padding: 10px 10px 0 0;
		text-align: right;
		.btn {
			background: @gray-lighter;
			font-size: 12px;

			&:hover {
				background: darken(@gray-lighter, 5%);
			}
		}
	}

	.halves-wrapper {
		position: relative;
		overflow-y: scroll;
	}
}
