// Spinner aka progress indicator
.ff-spin {
	//z-index: 0;
	position: relative;

	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		margin: -10px 0 0 -10px;
		overflow: hidden;
		border-radius: 50%;
		width: 20px;
		height: 20px;
		//border: 3px solid rgba(0, 0, 0, 0.1);
		border: 3px solid rgba(230,230,230,0.8);
		border-bottom: 3px solid @color-brand-green; //rgba(0, 0, 0, 0.4);
		animation: spin1 1s infinite linear;
		z-index: -1;

		@keyframes spin1 {
			100% {
				transform: rotate(360deg);
			}
		}
	}
}

// Spin over ffSearch should be on top
ff-search.ff-spin::before {
	z-index: 3;
}

ff-btn.ff-spin {
	.btn {
		opacity: 1;
	}
	&::before {
		z-index: 1;
	}
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		//		background: #fff;
		//		opacity: 0.7;
		background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 20%, rgba(255,255,255,0.5) 100%);
		border-radius: inherit;
	}
}

.ff-spin.ff-spin-big, .ff-dim-big > .ff-spin { // good spin without side effects
	&::before {
		width: 30px;
		height: 30px;
		margin: -15px 0 0 -15px;
		//		border-width: 4px;
		z-index: 2;
	}
}

.ff-spin-box {
	width: 100%;
	height: 30px;
	&::before {
		z-index: 0;
	}

	&.ff-spin-big {
		height: 60px;
	}
}

.ff-spin.ff-big-spin { // ugly spin with dimming side effect
	&::before {
		width: 30px;
		height: 30px;
		margin: -15px 0 0 -15px;
		//		border-width: 4px;
		z-index: 2;
	}
	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 20%, rgba(255,255,255,0.5) 100%);
		z-index: 1;
	}
}

/**
 * ffDim directive (dimmed element with loading progress indicator)
 */
.ff-dim {
	position: relative;
}

.ff-dimmer {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: radial-gradient(ellipse at center, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0.7) 20%, rgba(255,255,255,0.5) 100%);
	z-index: 2;

	.ff-dim-top > & {
		z-index: 100;
	}
}
