.ff-tip {
	position: absolute;
	top: -4px;
	left: 50%;
	min-width: 160px;
	z-index: 150;

	&.rc-marker-info {
		width: max-content;
		max-width: 220px;
	}

	opacity: 0;
	transform: translate3d(-50%, -50%, 0) scale(0);
	transition: opacity .15s ease, transform .15s ease;

	text-transform: none;

	.ff-tip-trigger:hover & {
		opacity: 1;
		transform: translate3d(-50%, -100%, 0) scale(1);

		&.ff-tip-bottom {
			transform: translate3d(-50%, 100%, 0) scale(1);
		}

		&.ff-tip-right {
			transform: translate3d(0, -50%, 0) scale(1);
		}
	}

	font: normal 12px/1 @font-family-sans-serif;
	background: @color-brand-green;
	color: @color-brand-green; // child border color
	box-shadow: -1px 1px 3px rgba(0,0,0, 0.3);
	border: 1px solid #fff;
	border-radius: 3px;
	padding: 5px;
	text-align: left;

	// Inner tip container for content styling
	.ff-tip-inner {
		color: #fff;
	}

	& > i { // Triangle
		position: absolute;
		display: block;
		width: 14px;
		height: 14px;
		left: 50%;
		bottom: -7px;
		transform: translateX(-50%);

		&::after, &::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 0;
			height: 0;
			border: 1px solid transparent;
			pointer-events: none;
		}
		&::after {
			left: 1px;
			bottom: 1px;
			border-width: 6px 6px 0 6px;
			border-top-color: initial;
		}
		&::before {
			border-width: 7px 7px 0 7px;
			border-top-color: #fff;
		}
	}

	// Bottom tip
	&.ff-tip-bottom {
		top: auto;
		bottom: -4px;
		transform: translate3d(-50%, 0, 0) scale(0);
		box-shadow: -1px -1px 3px rgba(0,0,0, 0.3);

		& > i { // Triangle
			top: -7px;
			bottom: auto;

			&::after, &::before {
				top: 0;
				bottom: auto;
			}
			&::before {
				border-width: 0 7px 7px 7px;
				border-top-color: transparent;
				border-bottom-color: #fff;
			}
			&::after {
				border-width: 0 6px 6px 6px;
				border-top-color: transparent;
				border-bottom-color: initial;
				top: 1px;
			}
		}
	}

	&.ff-tip-right {
		top: 50%;
		left: 100%;
		margin-left: 7px;
		transform: translate3d(-55%, -50%, 0) scale(0);
		box-shadow: 1px 1px 3px rgba(0,0,0, 0.3);

		& > i { // Triangle
			left: -7px;
			top: 50%;
			bottom: auto;
			transform: translateY(-50%);

			&::before {
				border-width: 7px 7px 7px 0;
				border-top-color: transparent;
				border-right-color: #fff;
			}
			&::after {
				border-width: 6px 6px 6px 0;
				border-top-color: transparent;
				border-right-color: initial;
				left: 1px;
			}
		}
	}

	// Color variants
	&.primary {
		background: @color-brand-blue;
		color: @color-brand-blue; // child border color
	}
	&.warning {
		background: @color-brand-orange;
		color: @color-brand-orange; // child border color
	}
	&.danger {
		background: @brand-danger;
		color: @brand-danger; // child border color
	}
	&.default {
		background: @gray-light;
		color: @gray-light; // child border color
	}

	h6 {
		margin: 0;
		text-transform: uppercase;
		font-weight: inherit;
	}

	a {
		color: #fff;
		text-decoration: underline;
	}
}

/**
 * ffHint directive - absolutely positioned placeholder with info icon. Shows ffTip on hover.
 */
.ff-hint {
	position: static;
	display: inline-block;
	width: 14px;
	height: 18px;
	padding: 0;
	line-height: 18px;
	text-align: right;

	.ff-icon {
		position: relative;
		font-size: 12px;
	}
}
