rc-sidebar {
	display: block;
	position: sticky;
	left: 0;
	width: @rc-sidebar-width-xs;
	background-color: #fff;

	@media (min-width: @screen-xs-min) {
		width: @rc-sidebar-width;
	}
}

// Sidebar container-scroller
.rc-sidebar-scroller {
	position: relative;
	height: 100%;
}

// Inner scrollable container
.rc-sidebar-scrollable {
	position: absolute;
	width: 100%;
}

// Row group header (Reservation Category)
.rc-row-group-header {
	display: flex;
	align-items: center;
	position: absolute;
	left: 0;
	width: 100%;
	height: @rc-row-group-height;
	padding: 0 5px;
	font-size: 11px;
	font-weight: 400;
	background: @color-brand-green;
	color: #fff;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;

	@media (min-width: @screen-xs-min) {
		padding: 0 10px;
	}
}

// Row header (Reservation Unit)
.rc-row-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: absolute;
	left: 0;
	width: 100%;
	height: @rc-row-height;
	padding-left: 5px;
	font-size: 12px;

	@media (min-width: @screen-xs-min) {
		padding-left: 10px;
		font-size: 14px;
	}
}
.rc-row-label { // text label inside .rc-row-header
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	white-space: normal;
	text-overflow: ellipsis;
	overflow: hidden;
	line-height: 1.2;

	.rc-enable-photos & { // with reservation units photos enabled
		-webkit-line-clamp: 3;
	}
}
.rc-row-img { // image inside .rc-row-header
	height: 100%;
	margin-left: 5px;

	@media (max-width: @screen-xs-min) {
		flex: 0 0 24px !important;
	}

	& > div {
		position: relative;
		height: 100%;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	ff-tip {
		min-width: 0;
		background: #fff;
		border-color: @color-brand-green;
		color: #fff;

		&.ff-tip-right > i::before {
			border-right-color: @color-brand-green;
		}

		h6 {
			margin-bottom: 3px;
			color: @text-color;
			font-weight: 300;
			font-size: 14px;
		}
		img {
			max-width: 200px;
			max-height: 150px;

			@media (min-width: 390px) {
				max-width: 266px;
				max-height: 200px;
			}

			@media (min-width: 620px) {
				max-width: 400px;
				max-height: 300px;
			}
		}
	}
}

// Horizontal grid
rc-sidebar rc-layer-grid-h {
	margin: 0 !important;
	width: 100%;
	height: 100%;
}
