/**
 * Styles for ffRichtext (textAngular) in email templates
 */
.ta-email .ta-text {
	max-width: 622px; // 600px (email width) + 10px (padding) + 2px (borders)
}

.ta-email .ta-text .ta-bind {
	padding: 10px;
	color: #505050;
	font-family: Helvetica, serif;
	font-size: 15px;
	line-height: 130%;
	text-align: left;

	a:link, a:visited {
		color: #00758C;
		font-weight: normal;
		text-decoration: underline;
	}

	img {
		display: inline;
		height: auto;
		max-width: 100%;
	}

	p {
		margin-top: 0;
		margin-bottom: 10px;
	}

	blockquote {
		margin: 0 0 15px 0;
		padding: 5px 20px;
		border-left: 3px solid #EEEEEE;
		font-style: italic;
	}

	ul, ol {
		margin-top: 0;
		margin-bottom: 10px;
	}

	h1, h2, h3, h4 {
		display: block;
		margin: 15px 0 5px 0;
		line-height: 100%;
		font-style: normal;
		font-weight: normal;
		letter-spacing: normal;
		text-align: left;
	}

	h1 {
		color: #404040;
		font-size: 28px;
	}
	h2 {
		color:#404040;
		font-size: 24px;
	}
	h3 {
		color: #202020;
		font-size: 20px;
	}
	h4 {
		color: #000000;
		font-size: 18px;
	}

	& > *:last-child {
		margin-bottom: 0;
	}
}
