/**
 * White-label custom LESS styles. Used for customizing look-and-feel for each client.
 * Different versions of this file are stored in:
 * https://github.com/TheSatelliteInc/Deskworks_ops
 *
 * Mobile/desktop version selectors:
 * .mobile-version
 * .desktop-version
 *
 * Sidebar visibility in desktop version:
 * .sidebar-hidden
 * .sidebar-visible
 */

@font-family-sans-serif: Ubuntu, Arial, Helvetica, sans-serif;

@color-brand-blue: #317da3;
@color-brand-orange: #bf7117;
@color-brand-green: #888;
@brand-danger: #cc3314;

@color-brand-title: #000;

@text-color: #000;

@sidebar-group-background: #dedede;
@sidebar-group-hover-background: #bbb;
@sidebar-group-hover-color: #000;
@sidebar-group-color: #000;

@sidebar-item-color: @text-color;
@sidebar-item-color-hover: @color-brand-blue;
@sidebar-item-hover-border-color: @color-brand-blue;
@sidebar-item-background-active: @color-brand-blue;
@sidebar-item-color-active: #fff;

@flex-form-heading-color: @color-brand-title;
@flex-form-border-radius: 1px;

//@ff-label-color: #999;

@dash-widget-header-bg: @color-brand-green;

@footer-height: 30px;

/* Header */
.header {
	font-size: 0;

	a {
		&:extend(.container);
		display: flex;
		//justify-content: center;
		align-items: center;
	}

	// Header logotype
	a::after {
		content: '';
		display: inline-block;
		width: 84px;
		height: 84px;
		background: url('logo.png') bottom no-repeat;
		background-size: 100%;
		margin: 0 auto;
	}
}

/**
 * Footer
 */
.footer {
	background: #fff;
	border-top: 1px solid rgba(136, 136, 136, 0.15);
	padding: 8px 0;
	text-align: center;

	span {
		&:extend(.container);
		display: block;
		text-transform: none;
		font-size: 0.9em;
		color: rgba(0,0,0,0.75);

		a {
			color: rgba(0,0,0,0.75);
			text-decoration: none;

			&:hover {
				color: rgba(0,0,0,1);
			}
		}
	}
}

/**
 * Sidebar
 */
.sidebar-whitelabel {
	font-size: 0;
	padding: 5px;

	// Sidebar logotype
	a::after {
		content: '';
		display: block;
		width: 64px;
		height: 64px;
		background: url('logo.png') no-repeat bottom;
		background-size: 100%;
		margin: 0 auto 0 5px;
	}
}

.sidebar-group-header {
	border-color: #999;
	// Green badge
	&::before {
		background: @sidebar-item-background-active;
	}
	&.active::after {
		color: #fff;
	}
}

