/**
 * Buttons
 */
.btn {
	border-radius: 0;
}

.btn-primary {
	background: none;
	color: @color-brand-blue;
	border: 1px solid @color-brand-blue;
	&:hover, &:active {
		background: @color-brand-blue;
		border: 1px solid @color-brand-blue;
	}
}

.btn-success {
	background: none;
	color: @color-brand-green;
	border: 1px solid @color-brand-green;
	&:hover, &:active {
		background: @color-brand-green;
		border: 1px solid @color-brand-green;
	}
}

.btn-warning {
	background: none;
	color: @color-brand-orange;
	border: 1px solid @color-brand-orange;
	&:hover, &:active {
		background: @color-brand-orange;
		border: 1px solid @color-brand-orange;
	}
}

.btn-danger {
	background: none;
	color: @brand-danger;
	border: 1px solid @brand-danger;
	&:hover, &:active {
		background: @brand-danger;
		border: 1px solid @brand-danger;
	}
}

[sense=collapse] .btn, .ff-btn-delete-cross .btn {
	border: 0;
}

/**
 * Other
 */
.page-title {
	font-weight: normal;
	font-size: 22px;
	text-transform: uppercase;
	color: @color-brand-title;
}

.step-title {
	color: @color-brand-green;
	font-weight: normal;
}

.center-list .center-name, .rental-type-list .rental-type-name, .rental-type-details .inventory .row > div:last-of-type, .rental-unit-list .rental-unit-name, .rental-type-details .inventory h5 {
	color: #000;
}

.center-list .center-address, .center-list .center-phone {
	color: @color-brand-green;
}

