/**
 * flexForm directive transcludes to panel with heading and body. Supports two-column header.
 */

@flex-form-border: 1px solid lighten(@panel-default-border, 3%);
@flex-form-border-radius: 3px;
@flex-form-padding-horizontal: 5px;
@flex-form-margin-bottom: 20px;

@flex-form-heading-color: @color-brand-blue;
@flex-form-heading-bg: lighten(@panel-default-heading-bg, 2%);

@flex-form-body-color: @text-color;
@flex-form-body-bg: lighten(@panel-default-heading-bg, 2%);

@ff-control-border: 1px solid @control-border-color;
@ff-control-border-radius: 0;
@ff-control-color: @text-color;
@ff-control-bg: #fff;
@ff-control-min-height: 30px;
@ff-control-padding: 5px;

@ff-label-border: @ff-control-border;
@ff-label-border-radius: @ff-control-border-radius;
@ff-label-color: @color-brand-green;
@ff-label-bg: @ff-control-bg;
@ff-label-required-color: @color-brand-orange;

@ff-date-cal-color: @color-brand-green;
@ff-date-cal-hover-color: @color-brand-blue;

@ff-block-inverse-color: @ff-label-bg;
@ff-block-inverse-bg: @ff-label-color;

@ff-block-inverse-alt-color: #fff;
@ff-block-inverse-alt-bg: @color-brand-blue;

/**
 * Includes
 */
@import "ff-row.less";
@import "validation/ng-validate-alert.less";
@import "ff-spin/ff-spin.less";
@import "controls/ff-controls.less";
@import "ng-collapse/ng-collapse.less";
@import "ff-pagination/ff-pagination.less";
@import "ff-sort/ff-sort.less";
@import "ff-tip/ff-tip.less";
@import "ff-tabs/ff-tabs.less";
@import "ff-section/ff-bulk-read-only.less";

/**
 * flex-form container
 */
.flex-form {
	border: @flex-form-border;
	border-radius: @flex-form-border-radius;
	display: flex;
	flex-direction: column;
	margin-left: -@flex-form-padding-horizontal;
	margin-right: -@flex-form-padding-horizontal;
	width: auto;
	margin-bottom: @flex-form-margin-bottom;

	// Files are being dragged over flex-form
	&.dragover {
		outline: 2px dashed @color-brand-green;
	}

	/**
	 * Panel Heading
	 */
	.panel-heading {
		padding: 8px @flex-form-padding-horizontal;
		background: @flex-form-heading-bg;
		border: 0;
		border-radius: @flex-form-border-radius @flex-form-border-radius 0 0;

		.panel-title {
			color: @flex-form-heading-color;
		}

		// Splits title to two columns at 992px or more
		@media (min-width: @screen-md-min) {
			display: flex;
			.panel-title {
				flex: 1 1 50%;
				&:last-child {
					text-align: right;
				}
				&:first-child { // Overlap &:last-child for the case when only one element is available
					text-align: left;
				}
			}
		}

		// ffHint within panel heading
		.ff-hint {
			position: relative;
			top: -1px;
			line-height: normal;
			height: auto;
			text-transform: none;
		}
	}

	/**
	 * Panel Body
	 */
	.panel-body {
		padding: 0 @flex-form-padding-horizontal 8px @flex-form-padding-horizontal;
		background: @flex-form-body-bg;
		color: @flex-form-body-color;
		border-radius: 0 0 @flex-form-border-radius @flex-form-border-radius;
		flex: 1 1 auto;
	}

	// Footer buttons like Add in flex-form
	.ff-footer-buttons {
		margin-top: 10px;
	}
}

/**
 * ffTwoColumns directive
 */
ff-two-columns {
	display: flex;
	flex-wrap: wrap;

	// When bottom margin on last one or two flex-forms is removed (because display: flex prevents margin collapsing),
	// It's necessary to add this class to ff-two-columns directive to make bottom margin
	&.ff-margin-bottom {
		margin-bottom: @flex-form-margin-bottom;
	}

	& > * {
		display: flex;
		flex: 1 1 auto; // One column mode for mobiles
		width: 100%;
	}

	& > ff-row-head.ff-row:nth-child(2) {
		display: none; // Hide second head (duplicate for 2nd column) in one column mode
	}

	& > .flex-form:last-child {
		margin-bottom: 0 !important; // Remove last bottom margin in ff-two-columns > .flex-form as it doesn't collapse with outer margins
	}

	&.panel {
		box-shadow: none; // ff-two-columns.panel is used to get bottom panel margin, but need to reset box-shadow
	}

	.flex-form {
		width: 100%;
	}

	.ff-flex-form-col { // column of flexForm directives
		margin-left: -5px;
		margin-right: -5px;
		.flex-form {
			margin-left: 0;
			margin-right: 0;
		}
	}

	@media (min-width: @screen-md-min) {
		& > ff-row-head.ff-row:nth-child(2) {
			display: flex;
		}

		& > .flex-form:nth-last-child(2):nth-child(odd) {
			margin-bottom: 0; // Remove second last bottom margin in ff-two-columns > .flex-form as it doesn't collapse with outer margins
		}
		& > .flex-form:nth-child(2) {
			margin-bottom: @line-height-computed;
		}

		& > * {
			display: flex;
			flex: 0 0 49.5%; // Two columns mode for desktop
			// Fix for ffTwoColumns > flexForm to compensate negative flexForm margins
			&.flex-form, &.ff-flex-form-col {
				flex: ~"0 0 calc(49.5% + " @flex-form-padding-horizontal * 1.5 ~")";
			}

			&:nth-child(even) {
				margin-left: 1%; // Gap between columns
			}
		}

		// 66% x 33% layout
	   &.ff-two-columns-2-1 {
		   & > :nth-child(odd) {
			   flex: 0 0 66%;
			   // Fix for ffTwoColumns > flexForm to compensate negative flexForm margins
			   &.flex-form, &.ff-flex-form-col {
				   flex: ~"0 0 calc(66% + " @flex-form-padding-horizontal * 1.5 ~")";
			   }
		   }
		   & > :nth-child(even) {
			   flex: 0 0 33%;
			   // Fix for ffTwoColumns > flexForm to compensate negative flexForm margins
			   &.flex-form, &.ff-flex-form-col {
				   flex: ~"0 0 calc(33% + " @flex-form-padding-horizontal * 1.5 ~")";
			   }
		   }
	   }

	   // 33% x 66% layout
	   &.ff-two-columns-1-2 {
		   & > :nth-child(odd) {
			   flex: 0 0 33%;
			   // Fix for ffTwoColumns > flexForm to compensate negative flexForm margins
			   &.flex-form, &.ff-flex-form-col {
				   flex: ~"0 0 calc(33% + " @flex-form-padding-horizontal * 1.5 ~")";
			   }
		   }
		   & > :nth-child(even) {
			   flex: 0 0 66%;
			   // Fix for ffTwoColumns > flexForm to compensate negative flexForm margins
			   &.flex-form, &.ff-flex-form-col {
				   flex: ~"0 0 calc(66% + " @flex-form-padding-horizontal * 1.5 ~")";
			   }
		   }
	   }
	}
}

/**
 * 2-columns class
 */
.ff-2-columns {
	display: flex;
	flex-wrap: wrap;

	& > * {
		display: flex;
		flex: 1 1 auto; // One column mode for mobiles
		width: 100%;
	}

	@media (min-width: @screen-md-min) {
		& > * {
			display: flex;
			flex: 0 0 50%; // three columns mode for desktop
		}
	}
}

/**
 * 3-columns class
 */
.ff-3-columns {
	display: flex;
	flex-wrap: wrap;

	& > * {
		display: flex;
		flex: 1 1 auto; // One column mode for mobiles
		width: 100%;
	}

	@media (min-width: @screen-md-min) {
		& > * {
			display: flex;
			flex: 0 0 33.33%; // three columns mode for desktop
		}
	}
}

// Class for centering content
.ff-center, .ff-center > .ff-label, .ff-center > .ff-static, .ff-center input {
	justify-content: center;
	text-align: center;
//	align-items: center;
}

// Class for right-aligning content
.ff-right, .ff-right > .ff-label, .ff-right > .ff-static, .ff-right input {
	justify-content: flex-end;
	text-align: right;
}

// Class for smaller font size on children content
.ff-small {
	.ff-control {
		font-size: 0.85em;
		padding: 1px 5px;
	}

	& > span {
		font-size: 0.9em;
	}
}

.ff-small .ff-label {
	padding: 1px 5px;
	font-size: 0.8em;
}

// Applied to container to get inner ff-controls with 2/3 of min height, useful for small invoices, e.g. Accounting / Bulk Invoicing
.ff-reduce-height {
	.ff-label, .ff-control {
		min-height: @ff-control-min-height * 2 / 3;
		padding: 1px 3px;
	}

	//textarea.form-control.ff-textarea {
	ff-textarea, ff-textarea& {
		min-height: 70px;
	}
}


// Class for flex-start aligning labels for textarea
.ff-flex-start {
	align-self: flex-start;
}

// Adds position: relative;
.ff-relative {
	position: relative;
}

// Removes padding
.ff-no-padding, .ff-no-padding > .ff-control {
	padding: 0;
}

// Removes bottom margin
.ff-no-margin-bottom, .ff-btn-group.ff-no-margin-bottom, .ff-btn-group.ff-no-margin-bottom.ff-section-buttons {
	margin-bottom: 0;
}

// Add vertical margins to visually separate blocks
.ff-vmargins {
	margin-top: 15px;
	margin-bottom: 15px;
}
.ff-tmargin {
	margin-top: 15px;
}
.ff-bmargin {
	margin-bottom: 15px;
}

// Absolutely positioned borders
.ff-ab-l, .ff-ab-r, .ff-ab-t, .ff-ab-b {
	display: block;
	position: absolute;
	background: @control-border-color;
	&.info {
		background: @color-brand-green;
	}
}
.ff-ab-l, .ff-ab-r {
	top: 0;
	width: 1px;
	height: 100%;
}
.ff-ab-t, .ff-ab-b {
	left: 0;
	width: 100%;
	height: 1px;
}
.ff-ab-l {
	left: 0;
}
.ff-ab-r {
	right: 0;
}
.ff-ab-t {
	top: 0;
}
.ff-ab-b {
	bottom: 0;
}
.ff-border-info .ff-label, .ff-border-info .ff-control {
	border-color: @color-brand-green;
}


// Absolutely positioned element with left alignment, centered vertically
.ff-abs-left-center {
	position: absolute;
	left: 5px;
	top: 50%;
	transform: translateY(-50%);
}
// Absolutely positioned element with right alignment, centered vertically
.ff-abs-right-center {
	position: absolute;
	right: 5px;
	top: 50%;
	transform: translateY(-50%);
}

/**
 * ffClickable - clickable ffRow, responds on hover
 */
.ff-clickable {
	cursor: pointer;

	// Navigate icon
	.sense-navigate {
		opacity: 0.5;
		z-index: 2;
		font-size: 14px;
	}

	&:hover {
		filter: brightness(98%);
		z-index: 2;

		.sense-navigate {
			opacity: 1;
			color: @color-brand-blue;

			&::before {
				transform: scale(1.2);
			}
		}
	}

	& > ff-static:last-of-type .ff-static {
		padding-right: 18px;
	}
}

// Element inner in ffStatic of ffLabel, has reduced height and margins (used in Availability Schedule feature)
.ff-inner {
	min-height: 20px;
	color: @text-color;

	.ff-checkbox {
		margin: 1px 5px;
	}
}

// Borders collapsing
.ff-bc-l {
	margin-left: -1px;
}
.ff-bc-t {
	margin-top: -1px;
}
