.floating-toolbar {
	position: absolute;
	top: 1px;
	right: 1px;
	background: #fff;
	box-shadow: 0 0 3px rgba(100,100,100,.5);
	border-radius: 3px;
	z-index: 2;
	opacity: 0;
	transition: opacity .15s;

	*:hover > & {
		opacity: 1;
	}

	.ff-btn-icon {
		width: 28px;
		height: 28px;
		margin: 0;

		.ff-btn {
			font-size: 18px;
		}
	}
}
