.res-header {
	line-height: 0;
	padding: 15px 5px 15px 50px;
	border-bottom: 1px solid @gray-lighter;

	@media (min-width: @screen-sm-min) {
		padding: 15px 15px 15px 15px;
	}

	small {
		line-height: 1;
		color: @gray-light;
		text-transform: none;
	}
	h3 {
		line-height: 1;
		font-weight: inherit;
		font-size: 20px;
		text-transform: uppercase;
		color: @color-brand-blue;
	}
}

.res-container {
	display: flex;
	flex-direction: column;
	overflow: auto; // to fix layout in Firefox
}

.res-body, .res-confirm-body {
	padding: 15px 5px;

	@media (min-width: @screen-sm-min) {
		padding: 15px;
	}
}

.res-body {
	min-height: 320px;

	.res-col-opts {
		flex: 0 1 160px;
		@media (min-width: 400px) {
			flex: 0 1 270px;
		}
	}

	.res-col-time {
		flex: 1 0 170px;
		position: relative;
		margin-left: 3px;

		@media (min-width: 400px) {
			flex: 0 2 500px;
		}

		@media (min-width: @screen-xs-min) {
			margin-left: 10px;
		}
		@media (min-width: @screen-sm-min) {
			margin-left: 15px;
		}
	}
}


res-reservation {
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0 0 @footer-height 0;
	font-weight: 300;
	text-transform: uppercase;

	.ff-control, ff-checkbox {
		text-transform: uppercase;
		font-weight: 300;
	}

	ff-textarea {
		min-height: 30px;

		.ff-control {
			text-transform: none;
		}
	}

	@media (max-width: 400px) { // compact reservation form
		.res-buttons {
			.btn {
				padding-right: 5px;
				padding-left: 5px;
			}
			ff-btn {
				margin-left: 0;
				margin-right: 0;
			}
			ff-btn[sense='save'], ff-btn[sense='remove'] {
				margin-right: -20px;
			}
		}

		.time-selector .clear-selection {
			padding-right: 0;
		}
	}
}

// Current member above time selector
.res-current-member {
	flex: 0 0 auto;
	color: @color-brand-orange;
	font-size: 16px;
	text-align: center;
}

// Make/Update/Cancel Reservation buttons
.res-buttons {
	margin-bottom: -8px;

	ff-btn {
		margin: 0 5px 8px 0;
	}
}

.res-confirm {
	overflow: auto;
}

.res-confirm-body {
	max-width: 640px;
}
