.rental-unit-list {
	@media (max-width: @screen-sm-max) {
		.panel-body {
			padding: 0;
		}
	}

	.rental-unit-name, .selected-date {
		margin: 0 0 5px 0;
		padding-left: 5px;
		font-size: 16px;
		font-weight: normal;
		color: @color-brand-orange;
	}

	.rental-unit-info {
		display: flex;
		flex-direction: column;

		ff-info {
			margin-top: auto;
			padding: 0 0 0 5px;
			&:extend(.small);
		}
	}

	.selected-date {
		line-height: 30px;
		margin: 0;
		color: @color-brand-green;
	}

	res-clock {
		max-width: 68px;
		max-height: 68px;
	}
}
