.dw-invoice {
	display: block;
 	flex-direction: column;

	& > * { // Workaround for Chrome 48-49 bug with zero-height child elements
		min-height: 0;
	}

	// Invoice with inverse head
	&.inverse-head {
		ff-row-head .ff-label {
			background: @color-brand-green;
			color: #fff;
		}
	}

	.ff-btn-group {
		position: relative;

		fieldset {
			text-align: right;
		}

		ff-btn {
			display: block;

			&.invisible {
				height: 0;
			}
		}
	}
}
