rc-header {
	display: block;
	position: sticky;
	top: 0;
	transform: translateZ(0);
	z-index: 1;

	.mode-day & {
		height: @rc-header-height-day;
	}
	.mode-week & {
		height: @rc-header-height-week;
	}

	@media @rc-mq-sticky {
		.mode-day & {
			top: @rc-toolbar-height-day;
		}
		.mode-week & {
			top: @rc-toolbar-height-week;
		}
	}
}

// Scroller container
.rc-header-scroller {
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden;
}

// Inner scrollable container
.rc-header-scrollable {
	position: absolute;
	height: 100%;
	background-color: #fff;
	margin-left: @rc-sidebar-width-xs;

	@media (min-width: @screen-xs-min) {
		margin-left: @rc-sidebar-width;
	}

	& > * {
		position: absolute;
	}
}

// Horizontal line
rc-header .rc-borders-h {
	border-top: 0;
	bottom: 5px;
}

// Vertical marks
rc-header .rc-borders-v {
	bottom: 0;
	height: 10px;
	border-color: @color-brand-blue;
}

// Time markers
.rc-time {
	.mode-day & {
		transform: translateX(-33%);

		&.rc-highlight {
			span:first-child {
				position: relative;
				z-index: 0;
				color: #fff;

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: -1px;
					bottom: -1px;
					left: -1px;
					right: -1px;
					z-index: -1;
					border-radius: 2px;
					background-color: @color-brand-green;
				}
			}
		}
	}
	.mode-week & {
		display: block;
		width: 0.5952381%;
		bottom: 10px;
		line-height: 1.3;
		z-index: 0;
		text-align: center;
		font-size: 10px;
		color: #fff;

		// Green plate
		&.rc-highlight::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: -1px;
			bottom: 0;
			z-index: -1;
			background-color: @color-brand-green;
		}
	}

	// AM/PM marker
	sup {
		left: 2px;
	}
}

// Weekday
.rc-weekday {
	padding: 5px;
	font-size: 18px;
	line-height: 1.15;
	text-align: center;
	color: @color-brand-green;
	background-color: @rc-border-light;
	border-top: 1px solid @color-brand-green;
	border-bottom: 1px solid @color-brand-green;
}

// Small calendar pictogram
rc-header .rc-pictogram {
	position: absolute;
	top: 0;
	right: 3px;
	min-width: 24px;
	border-width: 2px;
	font-size: 16px;

	.serifs {
		top: -5px;
		hr {
			flex: 0 0 2px;
			height: 6px;
		}
	}
}

// Boundary grid
rc-header rc-layer-grid-b {
	margin: 0 !important;
	width: 100%;
	height: 100%;
	pointer-events: none;
}
