.dash-widget-members-checked-in {
	.dash-avatar {
		flex: 0 0 @dash-members-checked-in-avatar-size + 2px;

		ff-img {
			height: @dash-members-checked-in-avatar-size;

			img {
				width: 100%;
				height: 100%;
			}
			@supports (object-fit: cover) {
				img {
					object-fit: cover;
				}
			}
		}

		.fa {
			font-size: 48px;
			margin: 0 auto;
			opacity: 0.1;
		}
	}

	.ff-clickable:hover .dash-avatar {
		z-index: 1;

		img {
			transform: none;
			border-radius: 0;
			z-index: 1;
		}
	}

	.ff-clickable > ff-static:last-of-type .ff-static {
		padding: 1px 5px;
	}
}
