membership-filter {
	flex-wrap: wrap;
	margin-bottom: 8px;

	&:extend(.ff-row);

	& > .mf-status:last-of-type {
		margin-left: 0;
	}

	.mf-all {
		flex: 0 0 210px;
	}

	.mf-role {
		flex: 0 0 210px;
		color: @color-brand-blue;
	}

	@media (min-width: 1080px) {
		.mf-all, .mf-role {
			flex: 0 0 auto;
			margin-right: 15px;
		}
	}

	//.mf-status {
	//	flex: 0 0 80px;
	//
	//	@media (min-width: @screen-xs-min) {
	//		margin-left: 20px;
	//	}
	//
	//	& > span {
	//		align-items: center;
	//	}
	//}

	&[layout="2col"] {
		.mf-all {
			order: 10;
		}
		@media (min-width: 1080px) {
			.mf-all, .mf-role {
				flex: 0 0 50%;
				margin-right: 0;
			}
		}
	}
}
