.message-board {
	.msg-sticky {
		position: sticky;
		top: 0;
		z-index: 5;
	}

	.msg-buttons {
		.ff-static {
			padding: 0;
			flex-flow: column;
			justify-content: center;
		}
		ff-btn {
			width: 26px;
			height: 26px;
			margin-left: 0;

			.ff-btn {
				font-size: 18px;
			}
		}
	}

	.msg-count {
		margin-left: auto;
		font-size: .85em;
	}

	.msg-container.active {
		&::before {
			content: '';
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			z-index: 1;
			outline: 4px solid @color-brand-blue;
			outline-offset: -4px;
			background: rgba(255,255,255,.4);
		}
	}

	.msg-meta {
		line-height: 1.2;
		margin-bottom: 2px;
	}

	.msg-ava {
		flex: 0 0 50px;

		.ff-control {
			padding: 0;
		}

		ff-img {
			height: 48px;
			margin-bottom: auto;
			overflow: hidden;
		}
	}

	.msg-name {
		margin-right: 5px;
		font-size: .9em;
		font-weight: bold;
	}
	.msg-time {
		margin-right: 5px;
		font-size: .8em;
		font-weight: bold;
		opacity: .5;
	}
	.msg-date {
		font-size: .75em;
		opacity: .5;
	}
}
