.style-buttons(@display) {
	ff-btn[sense=delete], ff-btn[sense=remove], ff-btn[sense=new], ff-btn[sense=add], ff-btn[sense=save],
	ff-btn[sense=edit], ff-btn[sense=upload], ff-btn[sense=clone], ff-btn[sense=import], ff-btn[sense=charge],
	ff-btn[sense=makeFavorite], ff-btn[sense=next], ff-btn[sense=collapseBtnPriv] {
		display: @display;
	}
}

.ff-bulk-read-only {
	.style-buttons(none);

	ff-btn[sense=view].hidden {
		display: inherit !important;
	}

	.ff-read-only-ignore {
		.style-buttons(inherit);

		ff-btn[sense=view].hidden {
			display: none !important;
		}
	}
}
