@dash-widget-header-color: @ff-block-inverse-alt-color;
@dash-widget-header-bg: @ff-block-inverse-alt-bg;
@dash-widget-gap-vertical: 12px;

@dash-members-checked-in-avatar-size: 48px;

/**
 * Common dashWidget directive
 */
.dash-widget {
	display: block;
	margin: @dash-widget-gap-vertical 0;
	padding: 10px 5px 10px 6px;
	background: @flex-form-body-bg;
	border: @flex-form-border;
	border-radius: @flex-form-border-radius;

	ff-two-columns & {
		margin-bottom: 0;
	}

	.hint-label {
		font-size: 0.75em;
		text-transform: uppercase;
	}
}

// dashWidget inner container
.dash-widget-inner {
	position: relative;
}

// dashWidget header
.dash-widget-header {
	display: block;
	margin-left: -1px;
	padding: 0 3px;
	border: 1px solid @dash-widget-header-bg;
	font-size: .9em;
	color: @dash-widget-header-color;
	background: @dash-widget-header-bg;

	em {
		font-style: normal;
		font-weight: bold;
	}
}

// dashWidget body
.dash-widget-body {
	display: block;
	min-height: @ff-control-min-height;

	ff-static em {
		font-style: normal;
		color: @color-brand-orange;
	}
}

// Force bottom gap between widgets
.dash-widget-gap-bottom {
	margin-bottom: @dash-widget-gap-vertical;
}

// Avatar for Users Currently Checked-In
ff-static.dash-avatar {
	flex: 0 0 30px;
	overflow: visible;
	z-index: 1;

	.ff-static {
		padding: 0;
		overflow: visible;
	}

	img {
		//transform: scale(2) translateX(-5px);
		transition: all .2s ease;
	}

	.ff-clickable:hover & {
		z-index: 2;

		img {
			transform: translateX(-11px) scale(1.8);
			border-radius: 1px;
			z-index: 2;
		}
	}
}

@import "widgets-user/dash-members-checked-in.less";
@import "widgets-user/dash-mobile-app.less";
