res-clock {
	display: block;
	width: 100%;
	height: 100%;

	circle {
		fill: @color-brand-orange;
	}
	.available {
		fill: @color-brand-green;
	}
	.reserved {
		fill: @color-brand-orange;
	}

	.hour { // hour marker
		stroke: #fff;
		stroke-width: 2;
	}

	.quarter { // quarter marker
		stroke: #fff;
		stroke-width: 6;
	}

	text {
		font-size: 48px;
		font-weight: bold;
		fill: #fff;
	}
}
