.center-list {
	@media (max-width: @screen-sm-max) {
		.panel-body {
			padding: 0;
		}
	}

	.center-info {
		margin-left: 10px;

		// Prevent wrapping center info
		& > * {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}

	.center-name {
		font-size: 16px;
		color: @color-brand-orange;
		margin: 0 0 5px 0;
	}

	.center-address, .center-phone {
		color: @color-brand-green;
	}
}
