@ff-dp-bg-color: #fff;

@ff-dp-btn-color: @color-brand-orange;
@ff-dp-btn-hover-color: #000;
@ff-dp-btn-bg: transparent;
@ff-dp-btn-border: 0;

@ff-dp-cal-color: @color-brand-blue;
@ff-dp-cal-hover-color: @color-brand-orange;
@ff-dp-cal-bg: @ff-dp-bg-color;
@ff-dp-cal-border: 1px solid @cal-border-light;

@ff-dp-week-color: @color-brand-green;

// Simple static datepicker
.ff-datepicker {
	background: @ff-dp-bg-color;
}

// Datepicker popup
.ff-datepicker-popup .ff-datepicker {
	position: absolute;
	width: 220px;
	font-size: 12px;
	z-index: 20;
	border: 1px solid @color-brand-blue;
	border-radius: @ff-control-border-radius;
	box-shadow: 0 0 5px fade(@color-brand-blue, 50%);
	outline: none;
}


// Calendar header
.ff-dp-cal-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	text-transform: uppercase;
	color: @ff-dp-btn-color;

	// All buttons in header
	button {
		flex: 0 0 auto;
		align-self: stretch;
		color: @ff-dp-btn-color;
		background: @ff-dp-btn-bg;
		border: @ff-dp-btn-border;
		transform: translateZ(0); // Hack to prevent buttons flashing in chrome
		transition: color .1s;

		&:hover, &:focus, &:hover:focus {
			color: @ff-dp-btn-hover-color;
			z-index: 1;
			outline: none;
		}
	}

	// Month/Year block at center
	div {
		span { // Month span
			padding: 10px 0;
		}

		button { // Year button
			padding: 0;
			margin-left: 5px;
			span { // Icon
				//font-size: 0.85em;
			}
		}
	}
}

// Years selection
.ff-dp-years {
	display: flex;
	flex-flow: row wrap;
	margin: 0 -4px 0 0; // Compensate .ff-dp-day border collapsing

	& > * {
		flex: 0 0 20%;
		-ms-flex: 0 0 12.6%;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: -1px -1px 0 0; // Border collapsing
	}
}

// Week days header
.ff-dp-cal-week {
	display: flex;
	flex-flow: row nowrap;

	& > * {
		flex: 0 0 14.2857%;
		display: flex;
		justify-content: center;
		align-items: center;
		color: @ff-dp-week-color;
	}
}

// Calendar body
.ff-dp-cal-body {
	display: flex;
	flex-flow: row wrap;
	margin: 1px -6px 0 0; // Compensate .ff-dp-day border collapsing

	& > * {
		width: 14.2857%;
		margin: -1px -1px 0 0; // Border collapsing
	}
}

.ff-dp-monthly {
	& > * {
		width: 32.75%;
	}
}

// Day button in calendar
.ff-dp-day, .ff-dp-year {
	padding: 0.4em 0.6em;
	color: @ff-dp-cal-color;
	background: @ff-dp-cal-bg;
	border: @ff-dp-cal-border;
	transition: all .1s;

	&.dimmed { // Dimmed - tail of previous/next month
		color: fade(@ff-dp-cal-color, 40%);
	}

	&:hover {
		color: @ff-dp-cal-hover-color;
		border-color: fade(@color-brand-orange, 40%);
		z-index: 1;
	}

	&:focus {
		outline: 1px dashed @color-brand-blue;
		outline-offset: -1px;
		z-index: 1;
	}

	// Immediately hide fading out elements of ngRepeat to prevent flashing
	&.ng-leave {
		display: none;
	}

	&.current {
		color: @ff-dp-cal-bg;
		background: @ff-dp-cal-color;
	}

	&:disabled {
		color: rgba(0, 0, 0, .2);
		background-color: #fcfcfc;
		border: @ff-dp-cal-border;
	}

	// Today marker
	&.today {
		color: #fff;
		position: relative;
		z-index: 0;

		&::before {
			content: '';
			position: absolute;
			display: block;
			top: 50%;
			left: 50%;
			transform: translate3d(-50%, -50%, 0);
			width: 17px;
			height: 17px;
			border-radius: 50%;
			background: @color-brand-orange;
			z-index: -1;
		}
	}
}

