rc-legend {
	margin: 3px 5px 0 5px;
	text-transform: uppercase;
	font-size: 10px;
	color: @gray-light;

	& > div {
		position: relative;
		width: 106px;
		margin: auto;
		padding-left: 14px;
		white-space: nowrap;

		// Color badge
		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 9px;
			height: 9px;
			margin-top: -5px;
			border-radius: 50%;
		}
	}

	// Color badges
	.rc-reserved::before {
		background: @rc-reserved-color;
	}
	.rc-own::before {
		background: @rc-reserved-own-color;
	}
	.rc-available::before {
		background: @rc-available-color;
		border: 1px solid @rc-border-dark;
	}
}
