/**
 * ngValidateAlert directive - validation error tooltips for FlexForms
 */
.ng-validate-alert {
	opacity: 0;
	&.visible {
		opacity: 1;
	}

	display: inline-block;
	position: absolute;
	top: 50%;
	right: 2px;
	min-width: 64px;
	max-width: 100%;
	transform: translateY(-50%) translateZ(0);
	z-index: 5;
	white-space: normal;
	transition: opacity 0.1s;
	pointer-events: none;

	font-size: 11px;
	font-weight: 300;
	line-height: 1.1;
	background: @color-brand-orange;
	color: #fff;
	box-shadow: -1px 1px 3px rgba(0,0,0, 0.3);
	border: 1px solid #fff;
	border-radius: 3px;
	padding: 3px;
	text-align: left;

	// Triangle
	&::after, &::before {
		content: '';
		position: absolute;
		top: 50%;
		right: -6px;
		width: 0;
		height: 0;
		margin-top: -6px;
		border: 1px solid transparent;
		pointer-events: none;
	}
	&::after {
		border-width: 6px 0 6px 6px;
		border-left-color: @color-brand-orange;
	}
	&::before {
		margin-right: -1px;
		margin-top: -7px;
		border-width: 7px 0 7px 7px;
		border-left-color: #fff;
	}

	&.top {
		top: 0;
		left: 0;
		right: auto;
		transform: translateY(-100%) translateZ(0);

		&::before, &::after {
			transform: rotate(90deg);
			top: auto;
			bottom: -11px;
			left: 6px;
			right: auto;
			margin: 0;
		}
		&::after {
			bottom: -9px;
			margin-left: 1px;
		}
	}
}
