/**
 * ffPagination directive - FlexForms pagination directive
 */
ff-pagination {
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;

	span {
		min-height: @ff-control-min-height;
		display: flex;
		flex: 0 0 20px;
		align-items: center;
		justify-content: center;

		cursor: pointer;
		color: @link-color;

		&:hover {
			color: @link-hover-color;
		}

		&.active {
			cursor: default;
			color: @text-color;
			font-weight: bold;
		}

		&.ff-spin::before {
			z-index: 1;
		}
	}
}
