.rental-type-list {

	@media (max-width: @screen-sm-max) {
		.panel-body {
			padding: 0;
		}
	}

	.rental-type-info {
		display: flex;
		flex-direction: column;
		margin-left: 10px;

		ff-info {
			margin-top: auto;
			padding: 0;
			&:extend(.small);
		}
	}

	.rental-type-name {
		margin: 0 0 5px 0;
		font-size: 16px;
		color: @color-brand-orange;
	}
}
