/**
 * ~~~~~~ Common CSS code ~~~~~~
 */
[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none !important;
}
/**
 * Main
 */
html {
  position: relative;
  min-height: 100%;
}
.main {
  display: block;
  padding: 0.1px 5px;
}
.sidebar-hidden .main {
  margin: 0 auto;
}
@media (min-width: 480px) {
  .main {
    max-width: 1170px;
    margin-left: 0;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.header {
  padding: 5px 0;
  font-size: 0;
}
.header a::after {
  content: '';
  display: block;
  width: 182px;
  height: 78px;
  background: url('logo.png') no-repeat;
  background-size: 100%;
  margin: 0 auto;
}
.landing .header {
  display: block;
}
.sidebar-visible .header {
  display: none;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 30px;
  padding: 0 !important;
  overflow: hidden;
  z-index: 10;
  background: #2b2d2e;
  color: #828586;
  line-height: 1.2;
}
.footer a {
  width: auto;
  padding: 0;
  color: inherit;
}
.hide-footer .footer {
  display: none;
}
a:hover {
  text-decoration: none;
}
.page-title {
  text-align: center;
  font-size: 24px;
  font-weight: 300;
  color: #bf7117;
  border-color: rgba(243, 243, 243, 0.75);
  border-style: solid;
  border-width: 1px 0;
  padding: 10px 35px;
}
@media (min-width: 768px) {
  .page-title {
    padding: 10px;
  }
}
.step-title {
  color: #bf7117;
  text-transform: uppercase;
  font-weight: 300;
}
/**
 * Forms
 */
.form-control {
  height: 30px;
  padding: 5px;
  border: 1px solid #e7e8e9;
  border-radius: 0;
  box-shadow: none;
}
.form-control:focus {
  position: relative;
  z-index: 2;
}
label {
  font-weight: normal;
  color: #888;
  margin: 0;
}
.req,
req::after {
  position: relative;
  top: -0.15em;
  font-size: 1em;
  color: #bf7117;
}
req::after {
  content: '*';
}
.form-control.ng-invalid {
  border-color: rgba(191, 113, 23, 0.7);
  z-index: 1;
  position: relative;
  border-radius: 2px;
}
.form-control.ng-invalid:focus {
  border-color: #bf7117;
  box-shadow: 0 0 6px rgba(191, 113, 23, 0.7);
  z-index: 2;
}
input[type=checkbox].ng-invalid {
  box-shadow: 0 0 1px 1px #bf7117;
}
input[type=checkbox].ng-invalid:focus {
  box-shadow: 0 0 1px 2px #bf7117;
}
::placeholder {
  color: #ddd !important;
  line-height: 18px;
}
/**
 * Buttons
 */
.btn {
  border-radius: 0;
  border: 0;
  text-transform: uppercase;
  font-weight: 400;
  transition: background 0.1s;
}
.btn-default {
  background: #eeeeee;
}
.btn-default:hover,
.btn-default:focus,
.btn-default:active {
  background: #e2e2e2;
}
.btn-warning:hover,
.btn-warning:focus,
.btn-warning:active {
  background: #bf6317;
}
.btn.disabled,
.btn[disabled],
fieldset[disabled] .btn {
  opacity: 0.3;
}
.buttons-top {
  margin: 5px 0 15px 0;
}
.buttons-top > button {
  margin: 5px 0;
}
.buttons-bottom {
  margin: 15px 0 15px 0;
}
.buttons-bottom > button {
  margin: 5px 0;
}
.btn-collapse > span:last-child {
  transform: scale(1.2) translateX(2px);
}
.btn-collapse.collapsed > span:last-child::before {
  content: '\e159';
}
