/*
 * Checkbox switcher
 */
.switcher {
  display: inline-block;
  position: relative;
  width: 100px;
  height: 30px;
  margin: 0;
  background: #888;
  z-index: 0;
  font-size: 18px;
  cursor: pointer;
  user-select: none;
}
.switcher::before {
  content: '';
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
  width: 96px;
  height: 26px;
  background: #fff;
  border-radius: 4px;
  z-index: -1;
}
.switcher input[type="checkbox"] {
  display: none;
}
.switcher div::before {
  content: '';
  display: block;
  position: absolute;
  top: 3px;
  left: 49px;
  width: 48px;
  height: 24px;
  background: #888;
  border-radius: 4px;
  z-index: -1;
  transition: all 0.1s ease;
}
.switcher div > span {
  display: block;
  position: absolute;
  top: 3px;
  left: 0;
  width: 50%;
  height: 100%;
  transition: all 0.1s ease;
  text-align: center;
  color: #888;
}
.switcher div > span:last-of-type {
  left: 50%;
  color: #fff;
}
.switcher input[type="checkbox"]:checked + div::before {
  left: 3px;
}
.switcher input[type="checkbox"]:checked + div > span:first-of-type {
  color: #fff;
}
.switcher input[type="checkbox"]:checked + div > span:last-of-type {
  color: #888;
}
.checkbox {
  margin: 0;
}
.checkbox input[type=checkbox] {
  position: static;
  vertical-align: middle;
  margin: -2px 5px 0 5px;
}
/**
 * Modal dialogs
 */
.modal-header .close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 15px;
  outline: 0;
  transition: opacity 0.1s;
  margin: 0;
}
.modal-header .close span {
  display: block;
  line-height: 20px;
  font-size: 36px;
  font-weight: 300;
}
/**
 * textAngular reach text editor
 */
.ta-editor {
  z-index: 1;
}
.ta-toolbar .btn {
  padding: 9px;
  font-size: 13px;
  line-height: 1;
  background: #fff;
  color: #777777;
  border: 1px solid #f2f2f2;
}
.ta-toolbar .btn.active {
  color: #000;
  z-index: 0;
}
.ta-toolbar .btn:hover {
  z-index: 0;
}
#toolbarWC,
#toolbarCC {
  font-size: 11px;
  color: #888;
  opacity: 1;
  background: transparent;
  min-width: 80px !important;
  padding-top: 11px;
}
/**
 * Proportional Boxes
 */
.box {
  position: relative;
}
.box::before {
  content: '';
  display: block;
  padding-top: 100%;
}
.box .box-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.box.ratio3x2::before {
  padding-top: 66.666%;
}
.box.ratio2x1::before {
  padding-top: 50%;
}
/**
 * Layers
 */
.layer-folder-rel {
  position: relative;
}
.layer-folder-abs {
  position: absolute;
}
.layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
/**
 * Panels
 */
.panel-title {
  color: #255f7c;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: normal;
}
/*
 * Spinner for loading resources
 */
.bgloader {
  z-index: 0;
  position: relative;
}
.bgloader::before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -10px 0 0 -10px;
  overflow: hidden;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  border: 3px solid rgba(0, 0, 0, 0.1);
  border-bottom: 3px solid rgba(0, 0, 0, 0.4);
  animation: spin1 1s infinite linear;
  z-index: -1;
}
@keyframes spin1 {
  100% {
    transform: rotate(360deg);
  }
}
/**
 * Other
 */
.modal-backdrop {
  bottom: 0;
}
.rotate {
