.itooltip {
  display: inline-block;
  position: absolute;
  top: -2px;
  left: 50%;
  right: auto;
  transform: translate3d(-50%, -100%, 0);
  z-index: 150;
  white-space: normal;
  transition: opacity 0.1s;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.1;
  background: #bf7117;
  color: #fff;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.3);
  border: 1px solid #fff;
  border-radius: 3px;
  padding: 5px;
  text-align: left;
}
.itooltip::after,
.itooltip::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -7px;
  width: 0;
  height: 0;
  margin: 0 0 0 -6px;
  border: 1px solid transparent;
  pointer-events: none;
}
.itooltip::after {
  border-width: 6px 6px 0 6px;
