/**
 * White-label custom LESS styles. Used for customizing look-and-feel for each client.
 * Different versions of this file are stored in:
 * https://github.com/TheSatelliteInc/Deskworks_ops
 *
 * Mobile/desktop version selectors:
 * .mobile-version
 * .desktop-version
 *
 * Sidebar visibility in desktop version:
 * .sidebar-hidden
 * .sidebar-visible
 */
/* Header */
.header {
  font-size: 0;
}
.header a {
  display: flex;
  align-items: center;
}
.header a::after {
  content: '';
  display: inline-block;
  width: 84px;
  height: 84px;
  background: url('logo.png') bottom no-repeat;
  background-size: 100%;
  margin: 0 auto;
}
/**
 * Footer
 */
.footer {
  background: #fff;
  border-top: 1px solid rgba(136, 136, 136, 0.15);
  padding: 8px 0;
  text-align: center;
}
.footer span {
  display: block;
  text-transform: none;
  font-size: 0.9em;
  color: rgba(0, 0, 0, 0.75);
}
.footer span a {
  color: rgba(0, 0, 0, 0.75);
  text-decoration: none;
}
.footer span a:hover {
  color: #000000;
}
/**
 * Sidebar
 */
.sidebar-whitelabel {
  font-size: 0;
  padding: 5px;
}
.sidebar-whitelabel a::after {
  content: '';
  display: block;
  width: 64px;
  height: 64px;
  background: url('logo.png') no-repeat bottom;
  background-size: 100%;
  margin: 0 auto 0 5px;
}
.sidebar-group-header {
  border-color: #999;
}
.sidebar-group-header::before {
  background: #317da3;
}
.sidebar-group-header.active::after {
  color: #fff;
}
/**
 * Buttons
 */
.btn {
  border-radius: 0;
}
.btn-primary {
  background: none;
  color: #317da3;
  border: 1px solid #317da3;
}
.btn-primary:hover,
.btn-primary:active {
  background: #317da3;
