.qb-connect {
	position: relative;
	width: 86px;
	height: @ff-control-min-height - 6px;
	padding: 2px;
	border-radius: 3px;
	background-color: #2c9f1c;
	transition: background-color .1s;

	&:hover, &:focus, &:active {
		background-color: #2c8418;
	}

	svg {
		width: 100%;
		height: 100%;

		path {
			fill: #fff;
		}
	}
}
