dw-select-user {
	display: block;
	position: relative;

	.ff-control {
		flex-direction: column;
		align-items: flex-start;
		line-height: 1;
	}

	.company {
		margin-bottom: 2px;
		font-size: 0.85em;
		font-weight: bold;
	}

	.ava {
		.company, .member {
			margin-right: 30px;
		}
	}

	// .avatar container
	.ff-static {
		position: relative;
	}

	.avatar {
		position: absolute;
		top: 50%;
		right: 1px;
		width: auto;
		height: 100%;
		max-height: 32px;
		z-index: 1;
		transition: 0.2s all ease;
		transform: translateY(-50%);
	}
	&:hover {
		.avatar {
			transform: translateY(-50%) scale(2) translateX(-17.5%);
			border-radius: 1px;
		}
	}
}
