.ff-label ff-sort {
	position: absolute;
	top: 0;
	right: 0;
	width: @ff-control-min-height;
	height: @ff-control-min-height;
	text-align: center;
	font-size: 14px;
	font-weight: bold;
	color: #777;
	cursor: pointer;

	&.ff-spin {
		&::before {
			z-index: 1;
		}
		.fa {
			opacity: 0;
		}
	}

	.fa {
		width: 100%;
		height: 100%;
		transition: opacity .15s;

		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			transition: transform .15s;
			z-index: 1;
		}
	}

	.fa-sort-asc, .fa-sort-desc {
		color: @color-brand-orange;
		font-size: 17px;
	}

	.fa-sort-asc::before {
		margin-top: 4px;
	}

	.fa-sort-desc::before {
		margin-top: -4px;
	}

	&:hover {
		.fa::before {
			transform: translate(-50%, -50%) scale(1.4);
		}
	}
}
