.dash-widget-mobile-app {
	.dash-widget-spin {
		display: none;
	}

	ff-btn[sense=close] {
		position: absolute;
		right: 0;
		top: 0;

		button {
			padding: 0 3px 2px 3px;
			font-size: 16px;
			color: @dash-widget-header-color;
			& > .fa {
				text-shadow: none;
			}
		}
	}

	.ios-share, .three-dots, .home-plus { // svg icons
		width: auto;
		height: 18px;
		margin: 0 2px;
		vertical-align: text-bottom;
	}
	.three-dots {
		height: 16px;
		margin: 0 3px;
	}
}
