/**
 * ffRow directive is a one-line container (display:flex) for any child items
 */
.ff-row {
	display: flex;
	position: relative;

	& > *:not(label), & > label > * {
		display: flex;
		flex: 1 1 1%; // Equally stretch all child items, can be overridden by flex="1 1 10px" directive
		margin: -1px 0 0 -1px;  // Border collapsing
	}

	label {
		display: flex;
		flex: 0 0 100%;
	}

	.ff-control {
		transition: all .1s;
	}
}

/**
 * ffCol directive is a multi-line container (display:flex) for any child items
 */
ff-col, .ff-col {
	flex-direction: column;
	display: flex;
}

// Active state used in collapsible lists like Members / Member Usage
.ff-row[ff-click] {

	.ff-control {
		cursor: pointer;
	}

	&.active {
		.ff-control {
			background: @color-brand-blue;
			color: #fff;
		}
	}
}
