/**
 * ~~~~~~ Common LESS variables ~~~~~~
 */

/**
 * Font Awesome
 */
@import '../../vendor/font-awesome/less/font-awesome.less';

@fa-font-path: '.';


/**
 * Desktop Calendar
 */
@cal-reserved-color: @color-brand-blue;         // Others reservations color in calendar
@cal-reserved-own-color: @color-brand-orange;   // Own reservations color in calendar

@cal-border-light: #f2f2f2;
@cal-border-dark: #b2b2b2;

/**
 * Desktop Time Selector in New Reservation dialog
 */
@time-selector-row-height: 37px;
@time-selector-time-col-width: 68px;

/**
 * Form-sheet controls
 */
@control-height: 30px;
@control-border-color: #e7e8e9;
@placeholder-color: #ddd;


/**
 * ~~~~~~ Common CSS code ~~~~~~
 */

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
	display: none !important; // because angular is loaded at the end of the body, this fixes ng-cloak
}

/**
 * Main
 */
html {
	position: relative;
	min-height: 100%;
}

// MAIN CONTENT BLOCK
.main {
	display: block;
	padding: 0.1px 5px;

	.sidebar-hidden & {
		margin: 0 auto;
	}

	@media (min-width: @screen-xs-min) {
		max-width: @container-lg;
		margin-left: 0;
		padding-left: 15px;
		padding-right: 15px;
	}
}

// HEADER BLOCK
.header {
	padding: 5px 0;
	font-size: 0;

	a::after { // header logotype
		content: '';
		display: block;
		width: 182px;
		height: 78px;
		background: url('logo.png') no-repeat;
		background-size: 100%;
		margin: 0 auto;
	}

	.landing & { // display header on landing pages
		display: block;
	}

	.sidebar-visible & { // hide header when sidebar is visible
		display: none;
	}
}

// FOOTER BLOCK
.footer {
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	bottom: 0;
	width: 100%;
	height: @footer-height;
	padding: 0 !important;
	overflow: hidden;
	z-index: 10;
	background: #2b2d2e;
	color: #828586;
	line-height: 1.2;

	a {
		width: auto;
		padding: 0;
		color: inherit;
	}

	.hide-footer & {
		display: none;
	}
}

a:hover {
	text-decoration: none;
}

.page-title {
	text-align: center;
	font-size: 24px;
	font-weight: 300;
	color: @color-brand-orange;
	border-color: rgba(243, 243, 243, 0.75);
	border-style: solid;
	border-width: 1px 0;
	padding: 10px 35px;

	@media (min-width: @screen-sm-min) {
		padding: 10px;
	}
}

.step-title {
	color: @color-brand-orange;
	text-transform: uppercase;
	font-weight: 300;
}

/**
 * Forms
 */
.form-control {
	height: @control-height;
	padding: 5px;
	border: 1px solid @control-border-color;
	border-radius: 0;
	box-shadow: none;

	&:focus {
		position: relative;
		z-index: 2;
	}
}

label {
	font-weight: normal;
	color: @color-brand-green;
	margin: 0;
}

// Required orange asterisk
.req {
	position: relative;
	top: -0.15em;
	font-size: 1em;
	color: @color-brand-orange;
}

// req directive
req {
	&::after {
		content: '*';
		&:extend(.req);
	}
}

.form-control.ng-invalid {
	border-color: fadeout(@color-brand-orange, 30%);
	z-index: 1;
	position: relative;
	border-radius: 2px;

	&:focus {
		border-color: @color-brand-orange;
		box-shadow: 0 0 6px fadeout(@color-brand-orange, 30%);
		z-index: 2;
	}
}

input[type=checkbox].ng-invalid {
	box-shadow: 0 0 1px 1px @color-brand-orange;

	&:focus {
		box-shadow: 0 0 1px 2px @color-brand-orange;
	}
}

::placeholder {
	color: @placeholder-color !important;
	line-height: 18px;
}

/**
 * Buttons
 */
.btn {
	border-radius: 0;
	border: 0;
	text-transform: uppercase;
	font-weight: 400;
	transition: background 0.1s;
}

.btn-default {
	background: @gray-lighter;
	&:hover, &:focus, &:active {
		background: darken(@gray-lighter, 5%);
	}
}

.btn-warning {
	&:hover, &:focus, &:active {
		background: spin(@color-brand-orange, -5);
	}
}

.btn.disabled, .btn[disabled], fieldset[disabled] .btn {
	opacity: 0.3;
}

// Top buttons block, has bottom margin
.buttons-top {
	margin: 5px 0 15px 0;
	& > button {
		margin: 5px 0;
	}
}
// Bottom buttons block, has top margin
.buttons-bottom {
	margin: 15px 0 15px 0;
	& > button {
		margin: 5px 0;
	}
}

.btn-collapse {
	& > span:last-child {
		transform: scale(1.2) translateX(2px);
	}
	&.collapsed {
		& > span:last-child {
			&::before {
				content: '\e159';
			}
		}
	}
}

/**
 * Selects
 */
select.form-control {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxMDAiIGhlaWdodD0iNTAiIHZpZXdCb3g9IjAgMCAxMDAgNTAiPgo8cG9seWdvbiBwb2ludHM9IjAsMCAxMDAsMCA1MCw1MCIgZmlsbD0iIzc3NyIvPgo8L3N2Zz4K');
	//<svg xmlns="http://www.w3.org/2000/svg" width="100" height="50" viewBox="0 0 100 50">
	//<polygon points="0,0 100,0 50,50" fill="#777"/>
	//</svg>
	background-repeat: no-repeat;
	background-position: ~"calc(100% - 5px) 50%";
	background-size: auto 0.4em;
	padding-right: 20px;

	// IE hack to prevent arrow background in addition to standard control view because IE doesn't support appearance
	@iehack: ~"screen\0";
	@media @iehack {
		background-image: none;
		padding: 5px;
	}
}

// Firefox hack to prevent redundant padding in selects
@-moz-document url-prefix() {
	select.form-control {
		padding: 0 20px 0 0;
	}
}

/*
 * Checkbox switcher
 */
.switcher {
	display: inline-block;
	position: relative;
	width: 100px;
	height: 30px;
	margin: 0;
	background: @color-brand-green;
	z-index: 0;
	font-size: 18px;
	cursor: pointer;
	user-select: none;

	&::before { // white area
		content: '';
		display: block;
		position: absolute;
		top: 2px;
		left: 2px;
		width: 96px;
		height: 26px;
		background: #fff;
		border-radius: 4px;
		z-index: -1;
	}

	input[type="checkbox"] {
		display: none;
	}

	div {
		&::before { // shifting switcher
			content: '';
			display: block;
			position: absolute;
			top: 3px;
			left: 49px;
			width: 48px;
			height: 24px;
			background: @color-brand-green;
			border-radius: 4px;
			z-index: -1;
			transition: all 0.1s ease;
		}

		& > span {
			display: block;
			position: absolute;
			top: 3px;
			left: 0;
			width: 50%;
			height: 100%;
			transition: all 0.1s ease;
			text-align: center;
			color: @color-brand-green;

			&:last-of-type {
				left: 50%;
				color: #fff;
			}
		}
	}

	input[type="checkbox"]:checked + div {
		&::before {
			left: 3px;
		}

		& > span:first-of-type {
			color: #fff;
		}

		& > span:last-of-type {
			color: @color-brand-green;
		}
	}
}


.checkbox {
	margin: 0;

	input[type=checkbox] {
		position: static;
		vertical-align: middle;
		margin: -2px 5px 0 5px;
	}
}

/**
 * Modal dialogs
 */
.modal-header {
	.close {
		position: absolute;
		top: 0;
		right: 0;
		padding: 15px;
		outline: 0;
		transition: opacity 0.1s;
		margin: 0;

		span {
			display: block;
			line-height: 20px;
			font-size: 36px;
			font-weight: 300;
		}
	}
}


/**
 * textAngular reach text editor
 */
.ta-editor {
	z-index: 1;
}
.ta-toolbar {
	.btn {
		padding: 9px;
		font-size: 13px;
		line-height: 1;
		background: #fff;
		color: @gray-light;
		border: 1px solid @cal-border-light;

		&.active {
			color: #000;
			z-index: 0;
		}
		&:hover {
			z-index: 0;
		}
	}
}

#toolbarWC, #toolbarCC {
	font-size: 11px;
	color: @color-brand-green;
	opacity: 1;
	background: transparent;
	min-width: 80px !important;
	padding-top: 11px;
}


/**
 * Proportional Boxes
 */
.box {
	position: relative;

	&::before {
		content: '';
		display: block;
		padding-top: 100%; // Default ratio: 1x1
	}

	.box-content {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}

	&.ratio3x2::before {
		padding-top: 66.666%;
	}

	&.ratio2x1::before {
		padding-top: 50%;
	}
}


/**
 * Layers
 */
.layer-folder-rel {
	position: relative;
}

.layer-folder-abs {
	position: absolute;
}

.layer {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}


/**
 * Panels
 */
.panel-title {
	color: @color-brand-blue-dark;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: normal;
}


/*
 * Spinner for loading resources
 */
.bgloader {
	z-index: 0;
	position: relative;
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		margin: -10px 0 0 -10px;
		overflow: hidden;
		border-radius: 50%;
		width: 20px;
		height: 20px;
		border: 3px solid rgba(0, 0, 0, 0.1);
		border-bottom: 3px solid rgba(0, 0, 0, 0.4);;
		animation: spin1 1s infinite linear;
		z-index: -1;

		@keyframes spin1 {
			100% {
				transform: rotate(360deg);
			}
		}
	}
}


/**
 * Other
 */
.modal-backdrop {
	bottom: 0;
}

.rotate {
	animation: spin1 1s infinite linear;

	@keyframes spin1 {
		100% {
			transform: rotate(360deg);
		}
	}
}

/**
 * Bootstrap tuning
 */
.text-primary {
	color: @color-brand-blue;
}

.text-success {
	color: @color-brand-green;
}

.text-warning {
	color: @color-brand-orange;
}

.text-text {
	color: @text-color;
}

/**
 * Badge
 */
.badge {
	position: relative;
	font-size: 0.9em;
	font-weight: bold;
	color: #fff;
	z-index: 0;

	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		width: 18px;
		height: 18px;
		transform: translate(-50%, -50%);
		border-radius: 50%;
		background-color: @text-color;
		z-index: -1;
	}

	&.primary::before {
		background-color: @color-brand-blue;
	}
	&.success::before {
		background-color: @color-brand-green;
	}
	&.warning::before {
		background-color: @color-brand-orange;
	}
	&.danger::before {
		background-color: @brand-danger;
	}
	&.default::before {
		background-color: @gray-light;
	}
}

/**
 * Tooltips
 */
.tooltip {
	.tooltip-inner {
		background-color: @color-brand-green;
		color: #fff;
		font-weight: 400;
	}

	// Warning tooltip
	&.warning {
		.tooltip-inner {
			background-color: @color-brand-orange;
		}
		&.bottom .tooltip-arrow {
			border-bottom-color: @color-brand-orange;
		}
		&.top .tooltip-arrow {
			border-top-color: @color-brand-orange;
		}
	}

	h6 {
		margin: 0;
		text-transform: uppercase;
	}

	&.bottom .tooltip-arrow {
		border-bottom-color: @color-brand-green;
	}
	&.top .tooltip-arrow {
		border-top-color: @color-brand-green;
	}
}

/**
 * Smooth collapse animation for ng-repeat and ng-if
 */
.animate-collapse {
	&.ng-enter, &.ng-leave {
		overflow: hidden;
		transition: max-height 1s cubic-bezier(0.5, 0, 1, 0), opacity .5s ease-in;
	}
	&.ng-leave {
		transition: max-height 1s cubic-bezier(0, 1, 0, 1) -.5s, opacity .5s;
	}
	&.ng-enter, &.ng-leave.ng-leave-active {
		max-height: 0;
		opacity: 0;
	}
	&.ng-leave, &.ng-enter.ng-enter-active {
		max-height: 9999px;
		opacity: 1;
	}
}


