/**
 * Styles for ffRichtext (textAngular) in html templates
 */
.ta-html .ta-text {
	max-width: 622px; // 600px (html width) + 10px (padding) + 2px (borders)
}

.ta-html, .ta-html .ta-text .ta-bind {
	p {
		margin-top: 0;
		margin-bottom: 10px;
	}

	blockquote {
		margin: 0 0 10px 0;
		padding: 5px 20px;
		border-left: 3px solid #EEEEEE;
		font-size: inherit;
		font-style: italic;
	}

	ul, ol {
		margin-top: 0;
		margin-bottom: 10px;
	}

	h1, h2, h3, h4 {
		display: block;
		margin: 15px 0 5px 0;
		line-height: 100%;
		font-style: normal;
		font-weight: normal;
		letter-spacing: normal;
		text-align: left;
	}

	h1 {
		color: #404040;
		font-size: 28px;
	}
	h2 {
		color:#404040;
		font-size: 24px;
	}
	h3 {
		color: #202020;
		font-size: 20px;
	}
	h4 {
		color: #000000;
		font-size: 18px;
	}
}

.ta-html .ta-text .ta-bind {
	padding: 10px;

	a:link, a:visited {
		text-decoration: underline;
	}

	img {
		display: inline;
		height: auto;
		max-width: 100%;
	}

	& > *:last-child {
		margin-bottom: 0;
	}
}

.ta-fake-header, .header.ta-fake-header {
	display: block;
	max-width: 622px;
	background: #fff;
	border: 1px solid #e7e8e9;
	margin-bottom: -1px;
	padding: 10px 0 15px 0;

	a {
		width: auto;
		pointer-events: none;
	}
}
