@import "../ff-datepicker/ff-datepicker.less";

/**
 * Styles for ff-controls like ff-text, ff-select, ff-textarea
 */
.ff-control, .ff-label {
	display: block;
	min-width: 0;
	height: auto;
	min-height: @ff-control-min-height;
	padding: @ff-control-padding;
	line-height: 1.1;
	color: @ff-control-color;
	background: @ff-control-bg;
	border: @ff-control-border;
	border-radius: @ff-control-border-radius;
}

// Disabled state for controls and labels
.ff-control:disabled, .ff-control[disabled], ff-label:disabled .ff-label, ff-label[disabled] .ff-label, fieldset:disabled .ff-control, fieldset:disabled .ff-label {
	opacity: 0.5;
	background-color: @ff-control-bg;
}

// Disabled state for Open Datepicker button
ff-date {
	button:disabled {
		opacity: 0.3;
		background-color: @ff-control-bg;
		color: @text-color !important;
	}
}

// Inner span with .nowrap class
.ff-control.nowrap > span, .nowrap > .ff-label, ff-checkbox.nowrap > span, ff-static.nowrap > span,
.nowrap .ff-control > span, .nowrap .ff-label > span, .nowrap ff-static > span {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

ff-label, ff-checkbox, ff-date, ff-email, ff-number, ff-price, ff-password, ff-radio, ff-select, ff-static, ff-text, ff-textarea, ff-time {
	position: relative; // For absolutely positioned .ng-validate-alert
}

/**
 * ffLabel directive - labels for ff-controls
 */
ff-label {
	display: flex;
	align-items: stretch;
	line-height: 1.1;

	ff-row-head & {
		// Hide overflow only in ff-row-head to prevent horizontal shifting header and rows. Outside ff-row-head, show overflow to prevent clipping validation alerts.
		overflow: hidden;
	}

	.ff-label {
		display: flex;
		align-items: center;
		font-size: 0.9em;
		color: @ff-label-color;
		width: 100%;
	}

	// ff-label.inverse style to invert bg/fg colors
	&.inverse .ff-label {
		color: @ff-block-inverse-color;
		background: @ff-block-inverse-bg;
	}

	// ff-label.inverse-alt style to invert bg/fg colors with alternative background (brand-blue)
	&.inverse-alt .ff-label {
		color: @ff-block-inverse-alt-color;
		background: @ff-block-inverse-alt-bg;
	}

	// Required asterisk
	&.ff-required .ff-label::after {
		content: '*';
		top: -0.2em;
		position: relative;
//		top: 20%;
//		position: absolute;
		color: @ff-label-required-color;
	}

	// Sortable column
	&:not(.ff-spin).ff-sort {
		cursor: pointer;

		&::after {
			content: '\f0dc';
			position: absolute;
			right: 5px;
			top: 50%;
			margin-top: -6px;
			font: 14px/1 FontAwesome;
			opacity: 0.5;
		}

		&:hover {
			&::after {
				opacity: 1;
			}
		}
	}

	// Active sortable column in ASC mode
	&:not(.ff-spin).ff-sort-asc {
		cursor: auto;

		&::after {
			content: '\f160';
			font-size: 13px;
			color: @color-brand-orange;
			opacity: 1;
		}
	}
}

::-webkit-input-placeholder {
	font-size: 0.9em;
}
:-moz-placeholder { /* Firefox 18- */
	font-size: 0.9em;
}
::-moz-placeholder {  /* Firefox 19+ */
	font-size: 0.9em;
}
:-ms-input-placeholder {
	font-size: 0.9em;
}


/**
 * ffTime directive - wrapper for standard input type="time"
 */
.ff-time {
	line-height: @ff-control-min-height - 4px;
	padding-top: 0;   // Fix redundant height in Chrome
	padding-bottom: 0;
}

ff-time {

	// Ghost wrapper pretending to be ff-time control to have the same paddings, but invisible. Needed for exact arrow icons aligning by 00:00 AM pattern
	.ff-time-wrapper {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		visibility: hidden;
	}

	// Internal blocks of 00:00 AM pattern
	.ff-time-wrapper > span {
		position: relative;
		display: inline-block;
	}

	// Arrow icons (initially transparent)
	ff-icon, ff-icon.fa {
		visibility: visible;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		opacity: 0;
		font-size: 20px;
		z-index: 2;
		user-select: none;
		cursor: pointer;
		line-height: 0.4;

		color: @color-brand-green;
		transition: color .1s, opacity .1s;

		&:hover {
			color: @color-brand-blue;
		}

		&:nth-of-type(odd) { // +1 hour / +15 mins
			top: -1px;
		}
		&:nth-of-type(even) { // -1 hour / -15 mins
			top: auto;
			bottom: 0;
		}
	}

	// Show arrow icons on input hover and icons hover (icons hover is done via JS)
	input:hover ~ div > span ff-icon, &.ff-show-arrows input ~ div > span ff-icon {
		opacity: 1;
	}

}

/**
 * ffDate directive - date input component
 */
ff-date {
	display: flex;

	// Calendar button
	button.fa {
		position: absolute;
		z-index: 2;
		top: 50%;
		right: 2px;
		transform: translateY(-50%);
		padding: @ff-control-padding;
		background: @ff-control-bg;
		color: @ff-date-cal-color;
		border: 0;
		transition: color 0.1s;
		&:hover, &:focus {
			color: @ff-date-cal-hover-color;
		}
	}

	ff-datepicker {
		position: absolute;
		right: 0;
		max-width: 220px;
		font-size: 0.85em;
		z-index: 20;
		border: 1px solid @color-brand-blue;
		border-radius: @ff-control-border-radius;
		box-shadow: 0 0 5px fade(@color-brand-blue, 50%);
		outline: none;
	}

	// ngForm wrapper
	& > div {
		width: 100%;
		display: flex;
	}

	input[type=date] {
		line-height: @ff-control-min-height;
		padding-top: 0;
		padding-bottom: 0;
	}
}

/**
 * ffTextarea directive - wrapper for standard textarea tag
 */
ff-textarea {
	min-height: 4 * @ff-control-min-height - 1; // If you change this, also need to change the reverse-formula in ff-textarea.directive.js

	span.ff-control {
		white-space: pre-wrap;
		align-items: flex-start;
	}
}

/**
 * ffCheckbox directive - wrapper for standard input type="checkbox"
 */
ff-checkbox, ff-radio {
	display: flex;
	align-items: center;
	min-height: @ff-control-min-height;

	// Checkbox edit-off replacement (Yes/No static field)
	span.ff-control {
		align-self: stretch;
	}
}

// Inner input element
input.ff-checkbox {
	flex: 0 0 12px;
	margin: @ff-control-padding;
}

// read-only placeholder
ff-checkbox {
	& > .fa.fa-check-square, & > .fa.fa-square-o {
		margin: 5px;
		color: @color-brand-blue;
		font-size: 14px;
	}
	& > .fa.fa-square-o {
		font-size: 15px;
	}
}

// read-only placeholder
ff-radio {
	& > .fa.fa-check-circle, & > .fa.fa-circle-o {
		margin: 5px;
		color: @color-brand-blue;
		font-size: 15px;
	}
}

// Include all checkbox which should be aligned with other checkboxes, so its label is clipped
ff-checkbox.ff-include-all {
	span {
		position: absolute; // Prevent clipping checkbox label
		top: 0;
		left: 28px;
		line-height: @ff-control-min-height;
		font-size: 0.9em;
		white-space: nowrap;
	}
}

/**
 * ffStatic directive - static text control which behaves like ff-text, but non-editable.
 */
ff-static {
	overflow: hidden;

	&.danger .ff-static, .danger & .ff-static {
		color: @brand-danger;
	}
}
.ff-static {
	display: flex;
	align-items: center;
	overflow: hidden
}

ff-static.no-crop, ff-static.no-crop .ff-static {
	overflow: visible;
}

/**
 * ffBtn directive - wrapper for standard button element.
 * Supports coloring and iconing by sense attribute and sizing by size attribute.
 */
ff-btn {
	display: inline-block;

	button {
		& > * {
			vertical-align: middle;
		}
	}

	&[size="sm"] button > span:first-of-type {
		display: none;
	}

	// Special class for not shrinking sm -> xs ffBtn size on mobiles
	.keep-sm-btn-size &[size="sm"] button > span:first-of-type {
		display: inline;
	}

	@media (min-width: (@screen-xs-min + @sidebar-width)) {
		&[size="sm"] button > span:first-of-type {
			display: inline;
		}
	}

	// Hide icon for button with countdown
	&.ff-countdown .fa {
		display: none;
	}
}
.ff-btn {
	align-self: center;
}
// Collapse button which looks like link
ff-btn[sense="collapse"] {
	button, button:hover, button:focus {
		transition: all 0.1s;
		background: transparent;
		color: @color-brand-blue;
		padding: 0;
		outline: none;
	}
	button:hover, button:focus {
		color: darken(@color-brand-blue, 10%);
	}
	button.btn-xs .fa {
		font-size: 28px;
		line-height: 1;
	}
	.fa {
		transition: all 0.1s;
		font-size: 1.4em;
		line-height: 1;
	}
	&.collapsed .fa {
		transform: rotate(-90deg);
	}
}
// Collapse button which looks like button, not like link
ff-btn[sense="collapseBtn"], ff-btn[sense="collapseBtnPriv"] {
	.fa {
		transition: all 0.1s;
		font-size: 1.1em;
		line-height: 1;
	}
	&.collapsed .fa {
		transform: rotate(-90deg);
	}
}
ff-btn[sense="link"], .ff-row > ff-btn[sense="link"] {
	margin: 0;
	padding: 0;

	button {
		margin: 0;
		padding-left: 5px;
		padding-right: 5px;
		text-decoration: none;
		text-transform: none;
		color: @link-color;
		font-size: inherit;
		&:hover {
			color: @link-hover-color;
		}
	}
}

.ff-row > ff-btn {
	margin-left: 5px;
}

// Favorite button should simulate disabled status
ff-btn[sense="favorite"] button {
	pointer-events: none;
}

// ffBtn[sense="delete"], but looks like red cross on transparent background
.ff-btn-delete-cross button {
	padding: 1px 5px 5px 5px;
	line-height: 0.5;
	font-size: 16px;
	background: transparent;
	color: @btn-danger-bg;

	&:hover, &:active, &:focus {
		background: transparent;
		color: darken(@btn-danger-bg, 10);
	}

	.fa {
		background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 25%, rgba(255,255,255,0) 100%);
	}
}
// Delete button immediate children of ff-row, center vertically, stick to right
ff-row > .ff-btn-delete-cross {
	position: absolute;
	height: 100%;
	right: 0;
	top: 50%;
	transform: translateY(-50%);
	margin: 0;
	z-index: 3;

	& .btn {
		height: 100%;
		padding-top: 0;
		padding-bottom: 0;

		&.active.focus, &.active:focus, &.focus, &:active.focus, &:active:focus, &:focus {
			outline-width: 1px;
			outline-style: solid;
		}
	}
}


ff-btn-group {
	display: block;
}

.ff-btn-group {
	align-items: center;
	font-size: 0;

	ff-btn {
		margin-left: 5px;
	}

	&.buttons-bottom, &.buttons-top {
		font-size: 0;
		ff-btn {
			margin: 3px 5px 3px 0;
			display: inline-block;
		}

		ff-info {
			font-size: 12px;
			display: inline-block;
			vertical-align: middle;
			margin-right: 5px;
		}
	}

	// Block buttons group which is shared for block of controls (e.g. Edit/Remove in User Profile)
	// Section buttons group which is shared for section with blocks of controls (e.g. Add in User Profile)
	&.ff-block-buttons, &.ff-section-buttons {
		margin: 5px 0;
		//flex-basis: 100%;

		ff-btn {
			margin-left: 0;
			margin-right: 5px;

			// Prevent collapsing buttons to icon only
			&[size="sm"] button > span:first-of-type {
				display: inline;
			}

			&[sense="collapse"] {
				margin-left: 5px;

				.fa {
					font-size: 18px;
				}
			}
		}
	}
}

// Button-icon (universal replacement for .ff-btn-delete-cross)
.ff-btn-icon {
	display: flex;

	.ff-btn {
		flex: 1 1 auto;
		align-self: stretch;
		border: none !important;
		outline: none !important;
		border-radius: 0;
		font-size: 14px;
		line-height: 0;
		padding: 0;

		.fa {
			text-shadow: 0 0 1px #fff, 0 0 1px #fff, 0 0 2px #fff, 0 0 2px #fff;
			transition: all .1s ease;
		}

		&, &:hover, &:focus {
			background: transparent;
		}

		&:hover, &:focus {
			.fa {
				transform: scale(1.4);
			}
		}
	}

	.btn-primary, &:hover, &:focus {
		&, &:hover, &:focus {
			color: @color-brand-blue;
		}
	}
	.btn-success, &:hover, &:focus {
		&, &:hover, &:focus {
			color: @color-brand-green;
		}
	}
	.btn-warning, &:hover, &:focus {
		&, &:hover, &:focus {
			color: @color-brand-orange;
		}
	}
	.btn-danger {
		&, &:hover, &:focus {
			color: @brand-danger;
		}
	}
}

// Direct flexForm > ffBtnGroup (top and bottom buttons groups, e.g. 'Add Pricelist' in Inventory / Product Edit)
flex-form .panel-body {
	& > .ff-section-buttons:first-child {
		margin-top: 0;
	}
	& > .ff-section-buttons:last-child {
		margin-bottom: 0;
	}
}

/**
 * ffRichText directive
 */
ff-richtext {
	text-angular {
		width: 100%;
	}

	.ta-scroll-window > .ta-bind, .ta-editor.ta-html, .ta-scroll-window.form-control {
		min-height: 150px;
	}

	&.ff-read-only .ta-toolbar {
		display: none;
	}
}

/**
 * ffImg directive
 */
ff-img {
	img {
		height: auto;
		max-width: 100%;
		width: 100%;
		align-self: center;
	}

	&[img-placeholder].ff-img-placeholder { // image placeholder
		&::before {
			content: attr(img-placeholder);
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			color: #bbb;
			background: #eee;
		}
	}
}

/**
 * ffIcon directive - wrapper for .fa
 */
.sense-checkInStatus {
	color: @color-brand-green;
}
.sense-checkOutStatus {
	color: @color-brand-orange;
}
.sense-breadcrumb {
	font-size: 0.9em;
}
.sense-checkMark {
	color: @color-brand-green;
}
.sense-error {
	color: @brand-danger;
}


/**
 * ffInfo directive - static text info block, just wrapper similar to div, but with special CSS styles, like padding and green color
 */
ff-info {
	display: block;
	padding: 6px 0;
	color: @color-brand-green;

	// Prevent redundant gap between panel header and ffInfo content
	flex-form .panel-body > &:first-child {
		padding-top: 0;
	}

	&.danger {
		color: @brand-danger;
	}
}

/**
 * ffSearch directive - text input with remote search feature
 */
ff-search {
	position: relative;
}

ff-search-options {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	max-height: 176px;
	overflow-y: scroll;
	z-index: 5;
	background: @ff-control-bg;
	border: 1px solid @color-brand-blue;
	border-radius: @ff-control-border-radius;
	color: @ff-control-color;
	box-shadow: 0 2px 8px fade(@color-brand-blue, 50);

	// ff-search-options.ffso-above is set by JS when options dropdown doesn't fit to the bottom of screen
	&.ffso-above {
		top: auto;
		bottom: 100%;
	}

	.ff-search-option {
		padding: 2px 5px;
		border-bottom: @ff-control-border;
		cursor: pointer;

		&:hover {
			background: darken(@ff-control-bg, 2);
		}

		&.active {
			background: fade(@color-brand-blue, 25);
		}

		&:last-of-type {
			border-bottom: 0;
		}
	}

	// search field matches in ffSearchOptions
	.match {
		color: @ff-label-color;

		span {
			color: @ff-control-color;
		}
	}
}

ff-search-match {
	position: absolute;
	top: 1px;
	bottom: 1px;
	left: 1px;
	right: 1px;
	padding: @ff-control-padding;
	padding-top: 0;
	padding-bottom: 0;
	overflow: hidden;

	background: @ff-control-bg;
	color: @ff-control-color;

	&.active {
		z-index: 3;
	}
}

ff-search-match, .ff-search-option {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	line-height: 1.2;

	h6 {
		font-size: 1em;
		font-weight: normal;
		margin: 0;
		padding: 0;
		color: lighten(@ff-control-color, 30%);
		flex: 0 0 100%;

		& > span {
			font-size: 0.9em;

			&:first-of-type {
				color: @ff-control-color;
				font-size: 1em;
			}
		}
	}

	& > div {
		font-size: 0.8em;
	}
}

/**
 * Two-column list of items like centers in simple reservation
 */
.ff-list {
	background: @ff-control-bg;
}

.ff-list-item {
	margin-top: -1px;
	padding: 5px 0;
	transition: all 0.1s;
	border: 1px solid transparent;
	border-color: @control-border-color transparent;

	// Hide first and last border in one-column mode on mobiles
	&:first-of-type {
		border-top-color: transparent;
	}
	&:last-of-type {
		border-bottom-color: transparent;
	}

	&:hover {
		background: fadeout(@color-brand-blue, 97%);
		border: 1px solid fadeout(@color-brand-blue, 65%);
		z-index: 2;
	}

	@media (min-width: @screen-md-min) {
		padding: 10px;
		border: 1px solid transparent;
	}
}


/**
 * ffCountdown directive
 */
.ff-countdown-time {
	letter-spacing: 2px;
}
