.sign-in {
	flex-form, ff-btn-group {
		max-width: 320px;
		margin-left: auto;
		margin-right: auto;
	}

	.panel-body {
		padding: 0 10px 10px 10px;
	}
	.panel-heading {
		padding: 10px;
	}

	.sign-up-block {
		margin-bottom: 10px;

		ff-btn[sense='signup'] {
			margin-left: auto;
		}
	}

	ff-btn[sense=link] .btn-link {
		padding-right: 0;
	}
}
