@import 'rc-date-selector.less';
@import 'rc-legend.less';
@import 'rc-options.less';

rc-toolbar {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	position: sticky;
	left: 0;
	padding: 0 10px 0 50px;
	z-index: 10;
	background-color: #fff;

	@media (min-width: @screen-sm-min) {
		padding: 0 10px;
	}

	@media (min-width: 580px) {
		flex-wrap: nowrap;
		justify-content: space-between;

		.mode-day & {
			height: @rc-toolbar-height-day;
		}
		.mode-week & {
			height: @rc-toolbar-height-week;
		}
	}

	@media @rc-mq-sticky {
		top: 0;
	}
}
