rc-adjustments {
	display: flex;
	align-items: center;
	position: sticky;
	top: 0;
	left: 0;
	width: @rc-sidebar-width-xs;
	padding: 3px 4px 0 3px;
	background-color: #fff;
	z-index: 1;

	@media @rc-mq-sticky {
		.mode-day & {
			top: @rc-toolbar-height-day;
		}
		.mode-week & {
			top: @rc-toolbar-height-week;
		}
	}

	.mode-day & {
		width: @rc-sidebar-width-xs - 24px;
		height: @rc-header-height-day;
		margin-top: -@rc-header-height-day;
	}
	.mode-week & {
		height: @rc-header-height-week;
		margin-top: -@rc-header-height-week;;
		padding: 10px 32px 0 3px;
	}

	@media (min-width: @screen-xs-min) {
		width: @rc-sidebar-width;
		.mode-day & {
			width: @rc-sidebar-width - 24px;
		}
	}

	// Zoom label
	ff-label {
		flex: 0 0 auto;
		margin-right: 4px;
	}
	.ff-label {
		border: 0;
		min-height: 0;
		padding: 0;
		font-size: 7px;
		font-weight: bold;
	}
}

// Calendar range slider
.rc-range {
	margin: 0;
	padding: 0;
}
