fd-toolbar {
	display: block;
	position: absolute;
	top: 0;
	right: 0;
	width: 42px;

	.dndDraggingSource {
		display: block !important;
	}

	.flex-form {
		border-color: @color-brand-blue;
	}

	.flex-form .panel-heading {
		padding: 5px 0;

		.panel-title:first-child {
			font-size: 8px;
			font-weight: bold;
			text-align: center;
		}
	}

	.flex-form .panel-body {
		padding: 0 5px 5px 5px;
		text-align: center;
	}

	.fd-tool {
		padding: 5px 0;
		font-size: 20px;
		line-height: 1;
		color: @color-brand-blue;
		transition: all .2s;
		cursor: move;

		&:hover {
			transform: scale(1.2);
		}
	}

	.fd-trash {
		font-size: 28px;
		color: @color-brand-orange;
		cursor: default;

		&:hover {
			transform: none;
		}

		&.dndDragover {
			transform: scale(1.4);
		}

		.dndPlaceholder {
			display: none !important;
		}
	}
}
