/**
 * Sidebar
 */

@sidebar-border-color: #f4f4f4;

@sidebar-group-background: @color-brand-blue;
@sidebar-group-color: #fff;
@sidebar-group-border-color: @sidebar-border-color;

@sidebar-group-hover-background: @color-brand-blue-dark;
@sidebar-group-hover-color: #fff;

@sidebar-item-background: #fff;
@sidebar-item-background-hover: #fff;
@sidebar-item-background-active: @color-brand-green;
@sidebar-item-border-color: @sidebar-border-color;
@sidebar-item-border-active: @color-brand-green;
@sidebar-item-hover-border-color: fade(@color-brand-green, 50%);
@sidebar-item-active-border-color: @sidebar-item-background-active;

@sidebar-item-color: @link-color;
@sidebar-item-color-hover: @color-brand-green;
@sidebar-item-color-active: #fff;

.menu-button {
	position: absolute;
	left: 0;
	top: 10px;
	font-size: 20px;
	width: 48px;
	height: 48px;
	cursor: pointer;
	z-index: 50;

	&::before, &::after {
		content: '';
		position: absolute;
		left: 50%;
		top: 50%;
		width: 1em;
		height: 0.2em;
		margin-top: -0.5em;
		margin-left: -0.5em;
	}

	&::before {
		height: 1em;
		background: @body-bg;
	}

	&::after {
		transition: all 0.1s;
		background: #000;
		box-shadow: 0 0.4em 0 0 #000, 0 0.8em 0 0 #000;
	}

	&:hover::after {
		background: darken(@color-brand-blue, 20%);
		box-shadow: 0 0.4em 0 0 darken(@color-brand-blue, 20%), 0 0.8em 0 0 darken(@color-brand-blue, 20%);
	}

	@media (min-width: @screen-sm-min) {
		display: none;
	}
}

// Page content which can be shifted to right by sidebar
.snap-content.snap-content {
	background: inherit;
	overflow: hidden;
}
.snap-content-scroller {
	height: 100%;
	overflow: auto;

	& > .ff-dimmer {
		z-index: 10;
	}
}
// Page content wrapper for adding sticky footer
.snap-content-wrapper {
	position: relative;
	min-height: 100%;
	padding-bottom: @footer-height + 10px;  // Footer
}
// Sidebar container
.snap-drawer {
	background: #fff;
	max-width: @sidebar-width;
	border-right: 1px solid darken(@gray-lighter, 5%);
}
// Draggable area for swiping sidebar from left
.snap-dragger {
	position: absolute;
	top: 0;
	left: 0;
	width: 16px;
	height: 100%;

	@media (min-width: @screen-sm-min) {
		display: none;
	}
}

/**
 * Sidebar container
 */
sidebar {
	background-color: #fff;
}

.sidebar-whitelabel {
	font-size: 0;
	border-bottom: 1px solid @border-gray-light;

	a {
		display: block;
	}

	a::after { // sidebar logotype
		content: '';
		display: block;
		width: 96px;
		height: 41px;
		background: url('logo.png') no-repeat;
		background-size: 100%;
		margin-left: 12px;
	}
}

/**
 * Group header and item common styles
 */
.sidebar-group-header, .sidebar-item {
	position: relative;
	display: block;
	margin: -1px 0 0 0;
	padding: 10px;
	border: solid @sidebar-border-color;
	border-width: 1px 0;
	text-decoration: none;
	text-transform: uppercase;
	font-size: 12px;
	line-height: 1;
	font-weight: normal;
	transition: all .1s;
	cursor: pointer;
}

/**
 * Group header
 */
.sidebar-group-header {
	background: @sidebar-group-background;
	color: @sidebar-group-color;
	border-color: @sidebar-group-border-color;
	transform: translateZ(0);

	&:hover {
		background: @sidebar-group-hover-background;
		color: @sidebar-group-hover-color;
	}

	// Arrow
	&::after {
		.fa-icon();
		content: '\f105';
		position: absolute;
		top: 50%;
		right: 12px;
		font-size: 18px;
		transform: translateY(-50%);
		transition: all 0.1s;
		z-index: 1;
	}

	// Green badge
	&::before {
		content: '';
		position: absolute;
		top: 50%;
		right: 8px;
		width: 16px;
		height: 16px;
		transform: translateY(-50%);
		background: @color-brand-green;
		border-radius: 50%;
		box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.75);
		display: none;
	}

	&.open {
		// Arrow rotation
		&::after {
			transform: translateY(-45%) rotate(90deg);
			right: 12.5px;
		}
	}

	&.active {
		// Green badge visibility
		&::before {
			display: block;
		}
	}
}

/**
 * Collapsible items container
 */
.sidebar-items {
	list-style: none;
	margin: 0;
	padding: 0;
}

.sidebar-items [ff-spin]::before, .sidebar-ff-spin::before {
	left: auto;
	right: 6px;
	z-index: 2;
}


/**
 * Item
 */
.sidebar-item {
	background: @sidebar-item-background;
	color: @sidebar-item-color;
	border-color: @sidebar-item-border-color;

	&, &:hover, &:focus, &:active {
		text-decoration: none;
	}

	&.disabled {
		opacity: 0.5;
		pointer-events: none;
	}

	&:hover {
		background: @sidebar-item-background-hover;
		color: @sidebar-item-color-hover;
		border-color: @sidebar-item-hover-border-color;
		z-index: 1;
	}

	&.active {
		background: @sidebar-item-background-active;
		color: @sidebar-item-color-active;
		border-color: @sidebar-item-active-border-color;
	}
}


/**
 * Sidebar contents
 */
.sidebar {

	.selectors {
		position: sticky;
		top: 0;
		z-index: 2;
		background: @body-bg;

		width: 100%;
		padding: 5px 5px 7px 5px;
		border-bottom: 1px solid darken(@gray-lighter, 0%);
		text-transform: uppercase;
		font-size: 0.75em;

		label, .dw-widget {
			display: inline-block;
			width: 100%;
			margin: 5px 0 0 0;

			&:first-child {
				margin-top: 0;
			}
		}

		select, ff-static > span {
			padding-top: 3px;
			padding-bottom: 2px;
			min-height: 24px;
			text-transform: none;
		}

		.user-selector {
			cursor: pointer;
		}
	}

	.desktop-version & .sign-up-link {
		display: none;
	}
}

// Cloaking sidebar during page load
body:not(.snapjs-left) .sidebar {
	display: none;
}

// Desktop sidebar #merge
@media (min-width: @screen-sm-min) {
	.sidebar-visible {
		.snap-content {
			transform: translate3d(0, 0, 0) !important;
			margin-left: @sidebar-width;
		}
		.snap-drawer {
			display: block !important;
		}
	}
}
