// Calendar pictogram
.rc-pictogram {
	position: relative;
	display: inline-block;
	min-width: 44px;
	margin: 4px 0 0 0;
	padding: 2px 3px 1px 2px;
	line-height: 1;
	align-self: flex-start;
	text-align: center;
	font-size: 26px;
	border: 3px solid @color-brand-orange;
	border-radius: 3px;

	.serifs { // Cal serifs
		position: absolute;
		top: -7px;
		left: 0;
		width: 100%;
		height: 9px;
		padding: 0 3px;
		justify-content: space-around;

		hr { // One serif
			flex: 0 0 3px;
			height: 9px;
			margin: 0;
			background: @color-brand-orange;
			border-radius: 100% / 1px;
		}
	}
}

rc-date-selector {
	font-weight: 300;

	// Day mode
	.day-mode {
		padding: 0 5px;

		.rc-pictogram {
			cursor: pointer;
		}
	}

	// Week Mode
	.week-mode {
		margin-top: 5px;
		padding: 0 5px;
		align-self: flex-start;

		.prev, .next {
			top: 0;
			height: 100%;
			padding-top: 8px;
		}
	}

	// Weekday with prev/next in day mode
	.weekday {
		position: relative;
		text-align: center;

		span {
			display: block;
			height: 24px;
			margin: 0;
			font-size: 16px;
			text-transform: uppercase;
			z-index: -1;
		}
	}

	// Prev/Next buttons
	.prev, .next {
		position: absolute;
		top: 0;
		width: 24px;
		height: 24px;
		margin: 0;
		padding: 3px 5px;
		cursor: pointer;
		color: #999;
		transition: color 0.1s;

		&:hover {
			color: #000;
		}

		&::before { // Left/right triangle
			content: '';
			display: inline-block;
			width: 0;
			height: 0;
			margin: auto 0;
			border-top: 7px solid transparent;
			border-bottom: 7px solid transparent;
		}

		&.disabled { // disabled due to settings.reservationCalendarLimit
			color: #ddd;
		}

		.ff-tip {
			min-width: 140px;
		}
	}

	.prev {
		left: -10px;
		text-align: left;
		&::before { // Left triangle
			border-right: 7px solid;
		}
	}

	.next {
		right: -10px;
		text-align: right;
		justify-content: flex-end;
		&::before { // Right triangle
			border-left: 7px solid;
		}
	}

	// Big orange date/week at right
	.bar {
		flex: 0 1 auto;
		align-self: flex-start;
		align-items: center;
		min-height: 35px;
		margin: 5px 0 0 10px;
		line-height: 1;
		color: @color-brand-orange;
		text-transform: uppercase;
		font-size: 20px;
	}
}
