rc-reservation {
	background-color: @rc-reserved-color;
	cursor: pointer;

	&.own {
		background-color: @rc-reserved-own-color;
	}

	// Additional behavior for reservation on active layer
	rc-layer-active & {
		background-color: transparent;
		border: 1px solid transparent;
		transition: all .2s;

		&.own {
			background-color: transparent;
		}

		&:hover {
			background-color: darken(spin(@rc-reserved-color, -5%), 10%);
			border-color: darken(spin(@rc-reserved-color, -5%), 15%);

			&.own {
				background-color: darken(spin(@rc-reserved-own-color, -5%), 10%);
				border-color: darken(spin(@rc-reserved-own-color, -5%), 15%);
			}
		}
	}

	.ff-tip {
		font-weight: normal;

		h6 {
			font-weight: bold;
			text-transform: none;

			.company {
				font-weight: normal;
			}
		}

		.ff-tip-inner > h6, .ff-tip-inner > div {
			margin-bottom: 2px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

// Username + reservation note marker
.rc-marker-info {
	font-weight: normal;
	text-align: center;
	transition: all .1s;

	.label {
		padding: 0;
		font-size: 9px;
		text-transform: uppercase;
		opacity: 0.75;
	}
}

// Start/end time markers
.rc-marker-start, .rc-marker-end {
	top: 50%;
	left: -4px;
	min-width: 36px;
	padding: 2px 1px;
	font-weight: 300;
	text-align: center;

	transform: translate3d(-50%, -50%, 0) scale(0);

	.ff-tip-trigger:hover & {
		transform: translate3d(-100%, -50%, 0) scale(1);
	}

	& > i { // Triangle
		width: 5px;
		height: 12px;
		left: auto;
		right: 0;
		top: 50%;
		bottom: auto;
		transform: translateY(-50%);

		&::after, &::before {
			left: 100%;
			top: 50%;
			margin: 0;
			border: 1px solid transparent;
			transform: translateY(-50%);
		}
		&::after {
			border-width: 5px 0 5px 4px;
			border-left-color: initial;
		}
		&::before {
			border-width: 6px 0 6px 5px;
			border-left-color: #fff;
		}
	}
}

.rc-marker-end {
	left: auto;
	right: -4px;

	transform: translate3d(50%, -50%, 0) scale(0);

	.ff-tip-trigger:hover & {
		transform: translate3d(100%, -50%, 0) scale(1);
	}

	& > i { // Triangle
		left: 0;
		right: auto;

		&::after, &::before {
			left: auto;
			right: 100%;
			transform: translateY(-50%) rotate(180deg);
		}
	}
}
