.rental-type-details {
	@media (max-width: @screen-sm-max) {
		.panel-body {
			padding: 0;
		}
	}

	.box {
		max-width: 640px;
		margin: 0 auto;
	}

	.rental-type {
		background: #fff;
	}

	.reservation-type-view {
		padding-top: 10px;
		border: @flex-form-border;
	}

	.rental-type-name {
		color: @color-brand-orange;
		font-size: 16px;
		font-weight: normal;
		padding-left: 15px;
		text-transform: uppercase;
	}

	.button-block {
		padding: 10px 15px;
		text-align: center;
	}

	.tabs-block ul.nav-pills {
		background: @flex-form-body-bg;
		text-align: center;

		li {
			float: none;
			display: inline-block;
		}

		a {
			background: none;
			color: #aaa;
			min-width: 100px;
			text-align: center;
			padding: 20px 15px;
			cursor: pointer;

			&:hover, &:focus {
				background: none;
			}
		}

		.active a {
			color: @color-brand-blue;
			cursor: auto;

			&::after, &::before {
				content: '';
				position: absolute;
				left: 50%;
				bottom: 0;
				width: 0;
				height: 0;
				border: 0 solid transparent;
			}
			&::before {
				margin-left: -9px;
				border: @flex-form-border;
				border-width: 0 9px 9px 9px;
				border-top-color: transparent;
				border-left-color: transparent;
				border-right-color: transparent;
			}
			&::after {
				bottom: -1px;
				margin-left: -8px;
				border-width: 0 8px 8px 8px;
				border-bottom-color: #fff;
			}
		}
	}

	.tab-pane {
		padding-bottom: 10px;
		max-width: 480px;
		margin: 0 auto;
	}

	.tab-content {
		padding: 15px 15px 5px 15px;
		border: @flex-form-border;
	}

	.inventory {
		color: @gray-light;

		h5 {
			color: @color-brand-green;
			margin-bottom: 0;
		}

		.row > div:first-of-type {
			width: 80%;
			padding-right: 0;
		}

		.row > div:last-of-type {
			width: 20%;
			padding-left: 0;
			text-align: right;
			color: @color-brand-orange;
		}
	}
}
