dw-alerts {
	position: fixed;
	width: 240px;
	right: 15px;
	bottom: @footer-height;
	z-index: 17010;
}

.dw-alert {
	position: relative;
	padding: 5px;
	margin-bottom: 5px;
	font-size: 12px;

	&.ng-enter, &.ng-leave {
		transition: opacity .5s;
	}
	&.ng-enter, &.ng-leave.ng-leave-active {
		opacity: 0;
	}
	&.ng-leave, &.ng-enter.ng-enter-active {
		opacity: 1;
	}

	&.alert-danger {
		background-color: @brand-danger;
		color: #fff;
	}

	&.alert-info {
		background-color: @color-brand-green;
		color: #fff;
	}

	button.close {
		top: -3px;
		right: 0;
		color: #fff;
		opacity: .5;

		&:hover, &:focus {
			opacity: 1;
		}
	}
}

.dw-alert-progress {
	display: block;
	position: absolute;
	top: 0;
	right: 2.5%;
	width: 95%;
	height: 1px;
	background-color: #fff;

	&.start {
		width: 0;
	}
}
