rc-layer-grid-h, rc-layer-grid-v, rc-layer-grid-b {
	pointer-events: none;
}

// Horizontal borders
.rc-borders-h {
	width: 100%;
	border-top: 1px solid @rc-border-dark;
	border-bottom: 1px solid @rc-border-dark;
}

// Vertical borders
.rc-borders-v {
	width: 0;
	height: 100%;
	border-right: 1px solid @rc-border-light;
}

// Vertical bound borders
.rc-borders-b {
	width: 0;
	height: 100%;
	border-right: 1px solid @color-brand-green;
}
