@rr-role-width: 60px;

rr-rule {
	position: relative;
	width: 146px;
	flex: 0 0 146px !important;
	margin: 0 !important;
	background: @ff-control-bg;

	&.rr-full-time-product {
		max-width: 292px;
		width: 292px;
		flex-grow: 1 !important;
	}

	&.rr-attached-user {
		max-width: 292px;
		width: 292px;
		flex-grow: 1 !important;
	}

	ff-row > ff-label {
		position: absolute;
		right: 100%;
		top: 0;
		bottom: 0;
		width: @rr-role-width;
		z-index: -1;
	}

	// spin in rule header
	ff-row-head.ff-spin::before {
		left: auto;
		right: 2px;
		width: 18px;
		height: 18px;
		border-color: rgba(230,230,230,.6);
		border-bottom-color: #fff;
		z-index: 1;
	}
}

.rr-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-self: flex-start;
	padding-left: @rr-role-width;
	z-index: 0;

	& > rr-rule ff-row-head ff-label::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 1px;
		height: 100%;
		background: #fff;
		z-index: 1;
	}
	& > rr-rule:first-of-type ff-row-head ff-label::after {
		display: none;
	}
}

.rr-calendar {
	ff-row-head {
		ff-label {
			overflow: visible;
		}
		ff-date .ff-control, ff-date button.fa-calendar, ff-text .ff-control {
			min-height: 16px;
			padding: 0 2px;
			font-size: 11px;
			color: #fff;
			background: transparent;
			border-color: #fff;
		}
		ff-datepicker {
			font-size: 12px;
			width: 200px;
		}
	}
}