.purchase {

	// Submit Payment row
	.payment-submit {
		margin-top: 10px;
		ff-btn {
			margin: 0;
		}
		label > span {
			align-self: center;
		}
	}

	// Payment/Discount/Refund collapse buttons
	ff-btn[sense='add-alt'] {
		margin: 10px 0;
		min-width: 180px;

		button {
			width: 100%;
			text-align: left;
		}
	}

	// ADD CHARGES, User Pricelist: Monthly Plans (The Satellite Felton)
	h6 span {
		text-transform: none;
	}

	// Using credit card ending: XXXX1111
	.credit-card {
		color: @color-brand-green;
	}
}
