@import (inline) '../../../node_modules/handsontable/dist/handsontable.css';

.itooltip {
	display: inline-block;
	position: absolute;
	top: -2px;
	left: 50%;
	right: auto;
	transform: translate3d(-50%, -100%, 0);
	z-index: 150;
	white-space: normal;
	transition: opacity 0.1s;

	font-size: 12px;
	font-weight: normal;
	line-height: 1.1;
	background: @color-brand-orange;
	color: #fff;
	box-shadow: -1px 1px 3px rgba(0,0,0, 0.3);
	border: 1px solid #fff;
	border-radius: 3px;
	padding: 5px;
	text-align: left;

	// Triangle
	&::after, &::before {
		content: '';
		position: absolute;
		left: 50%;
		bottom: -7px;
		width: 0;
		height: 0;
		margin: 0 0 0 -6px;
		border: 1px solid transparent;
		pointer-events: none;
	}
	&::after {
		border-width: 6px 6px 0 6px;
		border-top-color: @color-brand-orange;
		bottom: -6px;
		margin-left: -5px;
	}
	&::before {
		border-width: 7px 7px 0 7px;
		border-top-color: #fff;
	}

	// Green tooltip
	&.ok {
		background: @color-brand-green;
		&::after {
			border-top-color: @color-brand-green;
		}
	}

	&:hover {
		z-index: 151;
	}

	// Inline .itooltip within ffInfo directive
	ff-info & {
		position: static;
		transform: translate3d(0, -40%, 0);
	}
}

@htMarginTop: 42px;

handsontable {
	display: block;
}

#htImport {
	padding-top: @htMarginTop;

	// Fix layout mess for full-height handsontable
	.ht_master .wtHolder {
		width: 100% !important;
		height: auto !important;
		overflow-y: hidden !important;
		overflow-x: scroll !important;
	}

	// Fix table top header visibility
	.handsontable.ht_master thead, .handsontable.ht_master tr th {
		visibility: visible;
	}

	// Hide top header clone in handsontable, it's useful in limited height mode only
	.ht_clone_top {
		display: none;
	}

	// Hide corner clone, it's useful in limited height mode only
	.ht_clone_top_left_corner {
		display: none;
	}

	// Prevent clipping tooltip in top head
	.wtHolder {
		margin-top: -@htMarginTop;
		padding-top: @htMarginTop;
	}
	.ht_master {
		overflow: visible;
	}
	.ht_clone_corner, .ht_clone_left {
		top: @htMarginTop !important;
		z-index: 151;
	}

	.ht_clone_left, .ht_clone_left .wtHolder {
		height: 100% !important;
	}

	// Prevent clipping tooltip in body
	.ht_master td, .ht_master th {
		position: relative;
		overflow: visible;
	}

	// Reduce selects size
	.ff-select {
		width: auto;
		padding: 2px 18px 2px 2px;
		min-height: 0;
		min-width: 100%;
		max-width: 160px;
	}

	// Ignored rows
	.ht-ignore {
		opacity: 0.5;
	}

	// Row with headers (automatically detected)
	.ht-head-row {
		background: @color-brand-blue;
		color: #fff;
		opacity: 1;
	}

	// Invalid handsontable cell
	.ht-invalid {
		&::after {
			content: '';
			position: absolute;
			top: -1px;
			left: -1px;
			right: -1px;
			bottom: -1px;
			margin: auto;
			border: 1px solid @color-brand-orange;
		}

		// Invalid value marker
		.fa {
			position: absolute;
			right: 2px;
			top: 0;
			line-height: 22px;
			color: @color-brand-orange;
		}
	}

	// Head tooltip (fields mapping)
	.ht-head-tooltip {
		white-space: nowrap;

		ff-select {
			display: none;
		}

		&:hover ff-select {
			display: block;
		}
		&:hover span {
			display: none;
		}
	}

	// Body tooltip (validation)
	.ht-body-tooltip {
		min-width: 132px;
	}

	// Extra overlay for row headers
	.ht-row-extra {
		position: absolute;

		ff-icon {
			position: absolute;
			cursor: pointer;
			top: 0;
			right: 1px;
			min-width: 14px;
			text-align: center;
			line-height: 22px;
			transition: all 0.1s;

			&:hover {
				transform: scale(1.5);
			}

			&.sense-header {
				color: @color-brand-blue;
			}

			&.rotate {
				pointer-events: none;
			}

			&.ng-animate { // prevent ngIf from waiting for animation and delaying icons
				animation: none 0s;
				transition: 0s;
			}
		}
	}
}

/**
 * Entire import page
 */
#setupImport {

	// Warning icon
	.fa.sense-warning {
		color: @color-brand-orange;
	}
	.fa.sense-error {
		color: @brand-danger;
	}
}

import-handsontable {
	display: block;
	.ff-dimmer {
		z-index: 1000;
	}
}

.import-stats {
	padding-top: 3px;
	line-height: 1;

	& > div {
		display: flex;
	}

	.counter {
		font-size: 26px;
		font-weight: bold;
	}
	ff-icon {
		position: relative;
		top: 0px;
		left: 2px;
		font-size: 12px;
	}
	.text {
		position: relative;
		bottom: -14px;
		left: -7px;
		font-size: 9px;
		font-weight: bold;
		text-transform: uppercase;
	}

	.text-danger {
		color: @brand-danger;
	}

	.text-danger, .text-warning {
		.text {
			left: -9px;
		}
	}
}

.import-dashboard {
	ff-info {
		font-size: 13px;
		line-height: 1.2;
		align-items: center;

		& > span > ff-icon:first-of-type {
			font-size: 14px;
		}
	}

	@media (min-width: @screen-md-min) {
		.import-stats {
			flex: 0;
			margin-right: 15px;
		}
		ff-info {
			flex: 1;
			padding: 0;
		}
	}
}
