dw-check-in-widget, dw-manual-check-in-widget, dw-check-in-widget.sidebar-item , dw-manual-check-in-widget.sidebar-item {
	display: block;
	position: relative;
	cursor: auto;

	&:hover {
		color: @sidebar-item-color;
		background: @sidebar-item-background;
		border-color: @sidebar-item-border-color;
	}

	ff-btn-group {
		position: absolute;
		top: 50%;
		right: 5px;
		transform: translateY(-50%);
	}

	// Check In / Check Out buttons
	ff-btn[sense='checkIn'] button, ff-btn[sense='checkOut'] button {
		max-width: 46px;
		white-space: normal;
		line-height: 8px;
		padding: 4px 5px;
		font-size: 9px;
		font-weight: bold;
	}
}

dw-manual-check-in-widget {
	padding: 0 !important;
	border: 0 !important;
	z-index: 0 !important;

	& > .sidebar-item {
		display: flex;
		align-items: center;
		min-height: 34px;
		padding-top: 3px;
		padding-bottom: 2px;
		padding-right: 52px;

		&:hover {
			color: @sidebar-item-color;
			background: @sidebar-item-background;
			border-color: @sidebar-item-border-color !important;
			cursor: auto;
			z-index: 0;
		}
	}
}
