@rc-reserved-color: @color-brand-blue;         // Others reservations color in calendar
@rc-reserved-own-color: @color-brand-orange;   // Own reservations color in calendar
@rc-unavailable-color: #f2f2f2; // Unavailable space color in calendar
@rc-available-color: #fff; // Available space color in calendar

@rc-border-light: rgba(190, 190, 190, 0.2);
@rc-border-dark: #b2b2b2;

@rc-row-hover-color: #f9f9f9;

@rc-row-group-height: 16px; // Height of Rental Unit group header
@rc-row-height: 36px; // Height of calendar row

@rc-sidebar-width: 200px; // Width of calendar sidebar (rental unit names)
@rc-sidebar-width-xs: 120px; // Width of calendar sidebar (rental unit names)

// Height of toolbar
@rc-toolbar-height-day: 62px;
@rc-toolbar-height-week: 52px;

// Height of time header
@rc-header-height-day: 30px;
@rc-header-height-week: 60px;

// Grid inner element width. Width of column => 100% / 48
//@rc-grid-inner-width-day: 1670px;
//@rc-grid-inner-width-week: 2400px;

// Media query to make calendar with sticky header
@rc-mq-sticky: ~"(min-height: 320px) and (min-width: 580px)";

@import 'toolbar/rc-toolbar.less';
@import 'sidebar/rc-sidebar.less';
@import 'header/rc-adjustments.less';
@import 'header/rc-header.less';
@import 'layers/rc-layer-grid.less';
@import 'layers/rc-availability.less';
@import 'layers/rc-reservation.less';
@import 'layers/rc-staff-only.less';

rc-calendar {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 0 0 @footer-height 0;

	font-weight: 300;
	text-transform: uppercase;

	ff-select {
		.ff-select.ff-select {
			padding-right: 20px;
			text-transform: uppercase;
		}
	}
}

// Scroller container
.rc-scroller {
	position: relative;
	width: 100%;
	height: 100%;
	overflow: auto;
}

// Reservation calendar layer
.rc-layer {
	display: block;
	position: absolute;
	margin-left: @rc-sidebar-width-xs;

	@media (min-width: @screen-xs-min) {
		margin-left: @rc-sidebar-width;
	}

	& > * {
		position: absolute;
	}
}

// Reservation calendar background layer (unavailable space)
rc-layer-background {
	background-color: @rc-unavailable-color;
}
